import {Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {fadeTrigger} from '../../../../animations/fade.animation';
import {HttpProfileService} from 'ngx-uaa';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [fadeTrigger]
})
export class HomeComponent implements OnInit, OnDestroy {

  myDataModalOpened;
  personModalOpened = false;
  showVideoCall = false;
  networkingModalOpened = false;

  hasPerms = false;

  constructor(@Inject('i18n_ASSETS_PATH') public i18nASSETSPATH: string,
              private httpProfileService: HttpProfileService) {
  }

  ngOnInit() {
    this.httpProfileService.__canDo('PERSON', 'READ').subscribe((response) => {
      this.hasPerms = response as boolean;
    });
  }

  onNavItemSelected(event) {
    switch (event) {
      case 'profile': {
        this.myDataModalOpened = true;
        break;
      }
      case 'person': {
        this.personModalOpened = true;
        break;
      }
      case 'check-network': {
        this.networkingModalOpened = true;
        break;
      }
    }
  }

  ngOnDestroy(): void {

  }
}
