import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {CBooksList} from '../../interfaces/cBooksList';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CCardsLoading} from '../../interfaces/cCardsLoading';
import {environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CreditCardLoadingService {

  data : CCardsLoading[];
  rootURL = environment.baseUrl;
  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/creditcard/transaction/loading`);
  }

  getCreditCardId(id: number) {
    return this.http.get<CCardsLoading>(`${this.rootURL}/find/transaction/creditcard/loading/` + id);
  }
}
