import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'extractTagName'})
export class ExtractTagNamePipe implements PipeTransform {
  transform(value: string): string {

    let result;

    let splitted = value.split('.');

    if (splitted.length <= 1) {
      splitted = value.split('@');
    }
    if (splitted.length <= 1) {
      splitted = value.split(' ');
    }
    if (splitted.length <= 1) {
      splitted = value.split('-');
    }

    switch (splitted.length) {
      case 1: {
        result = splitted[0].substring(0, 2);
        break;
      }
      case 2: {
        result = splitted[0].charAt(0) + splitted[1].substring(0, 2);
        break;
      }
      default: {
        result = splitted[0].charAt(0) + splitted[1].charAt(0) + splitted[2].charAt(0);
        break;
      }
    }
    return result.toUpperCase();
  }
}
