import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent } from 'ng2-smart-table/ng2-smart-table.component';
import { CBooksIngestion } from '../../interfaces/cBooksIngestion';
import { ChequeBookDeliveryService } from '../../services/http/cheque-book-delivery.service';
import { ChequeBookIngestionService } from '../../services/http/cheque-book-ingestion.service';
import { environment } from '../../../../../environments/environment';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-cheque-books-ingestion',
  templateUrl: './cheque-books-ingestion.component.html',
  styleUrls: ['./cheque-books-ingestion.component.scss']
})
export class ChequeBooksIngestionComponent implements OnInit {
  rootURL = environment.baseUrl;
  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  displaySearch = true;
  data: CBooksIngestion [];
  settings = {
    actions: {
      edit: false,
      add: false,
      delete: false,
      position: 'right',
        columnTitle: 'Action'
      },

    columns: {
        id: {
          title: 'Id',
          filter: false,
          editable: true,
          addable: false
        },
        status: {
          title: 'status',
          filter: false,
          editable: true,
          addable: false
        },
        bankOperatorId: {
          title: 'bankOperatorId',
          filter: false,
          editable: true,
          addable: false
        },
        chequeBookId: {
          title: 'chequeBookId',
          filter: false,
          editable: true,
          addable: false
        }
      }
  };

  id:any="";
  constructor(private http: HttpClient,   private route: ActivatedRoute, private cbIngestionService: ChequeBookIngestionService){
    this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      console.log(this.id);
      if(this.id != null){this.loadDataByCBId(); this.displaySearch=false;}else{this.loadData();}
    });
  }

  onSearch(query: string = '') {
    console.log(query);
    if(query != ''){
      this.http.get<CBooksIngestion>(this.rootURL+'/search/transaction/chequebook/ingestion/'+query).subscribe(
        res => {
          console.log(res);
          this.data = res['data'];
        },
        (err: HttpErrorResponse) => {
            if (err.error instanceof Error) {
              console.log("Client-side error occured.");
            } else {
              console.log("Server-side error occured.");
            }
          });
        //console.log(this.actionValue);
        }else{
          this.loadData();
      }
    }

  ngOnChanges(){
   // this.loadData();
  }

  loadData(){
    this.cbIngestionService.getAll().subscribe((data : CBooksIngestion[]) => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  //Load data by chequebookid
  loadDataByCBId(){
    this.cbIngestionService.getCheckBookId(this.id).subscribe(data  => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  ngOnInit(){
    //this.loadData();
  };
}
