import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {CBooksList} from '../../interfaces/cBooksList';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CBooksDelivery} from '../../interfaces/cBooksDelivery';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ChequeBookDeliveryService {

  data : CBooksDelivery[];
  rootURL = environment.baseUrl
  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/chequebook/transaction/delivery`);
  }

  getCheckBookId(id: number) {
    return this.http.get<CBooksDelivery>(`${this.rootURL}/find/transaction/chequebook/delivery/` + id);
  }

}
