import {Component, OnInit} from '@angular/core';
import {CallCenterService} from '../../services/http/call-center.service';
import {IClient} from '../../interfaces/iClient';
import {CallcenterClientStatusService} from '../../services/callcenter-client-status.service';
import {WEBSOCKETAGENTSTATUS} from '../../enums/sockets/websocketagentstatus.enum';
import {VrWebSocketService} from '../../services/socket/vr-web-socket.service';

@Component({
  selector: 'app-client-list',
  templateUrl: './client-list.component.html',
  styleUrls: ['./client-list.component.scss']
})
export class ClientListComponent implements OnInit {

  /*  WEBSOCKETAGENTSTATUS: WEBSOCKETAGENTSTATUS;*/

  refreshing: boolean = false;

  clients: IClient[];

  colorMap: Map<string, string> = new Map<string, string>();
  complementaryColorMap: Map<string, string> = new Map<string, string>();

  query: string;

  constructor(private callCenterService: CallCenterService,
              private callcenterClientStatusService: CallcenterClientStatusService) {
  }

  ngOnInit() {
    this.callcenterClientStatusService.getCallcenterClientsStatusBS().subscribe((next) => {
      this.clients = next;
      this.refreshing = true;     // solo per lo spinner
      setTimeout(() => {
        this.refreshing = false;
      }, 1000);
    });

    setTimeout(() => { // per sicurezza, a volte, dopo la login non ha i dati aggiornati.
      this.refresh();
    }, 2000);
  }

  refresh() {
    this.callcenterClientStatusService.refreshCallcenterClientsStatus();
  }

  getClassFromStatus(agentStatus: WEBSOCKETAGENTSTATUS) {
    switch (agentStatus) {
      case WEBSOCKETAGENTSTATUS.FREE: {
        return 'badge badge-success';
        break;
      }
      case WEBSOCKETAGENTSTATUS.BUSY: {
        return 'badge badge-danger';
        break;
      }
      case WEBSOCKETAGENTSTATUS.PAUSE: {
        return 'badge badge-warning';
        break;
      }
      case null: {    // i totem hanno lo stato null
        return 'badge badge-success';
        break;
      }
    }
  }

  getColor(tagName) {
    let colour = this.colorMap.get(tagName);
    if (!colour) {
      colour = '#';
      let hash = 0;
      for (let i = 0; i < tagName.length; i++) {
        hash = tagName.charCodeAt(i) + ((hash << 5) - hash);
      }
      for (let i = 0; i < 3; i++) {
        const value = (hash >> (i * 8)) & 0xFF;
        colour += ('00' + value.toString(16)).substr(-2);
      }
      this.colorMap.set(tagName, colour);
      return colour;
    } else {
      return colour;
    }
  }

  getComplementaryBWColor(tagName) {
    let complementaryColour = this.complementaryColorMap.get(tagName);
    if (!complementaryColour) {
      let hex = this.getColor(tagName);
      if (hex.indexOf('#') === 0) {
        hex = hex.slice(1);
      }
      // convert 3-digit hex to 6-digits.
      if (hex.length === 3) {
        hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
      }
      if (hex.length !== 6) {
        // console.log('Invalid HEX color.');
        return '#000000';
      }
      const r = parseInt(hex.slice(0, 2), 16);
      const g = parseInt(hex.slice(2, 4), 16);
      const b = parseInt(hex.slice(4, 6), 16);
      complementaryColour = (r * 0.299 + g * 0.587 + b * 0.114) > 186 ? '#000000' : '#FFFFFF';

      this.complementaryColorMap.set(tagName, complementaryColour);
      return complementaryColour;
    } else {
      return complementaryColour;
    }
  }

  onSearch(query: string) {
    this.query = query;
  }
}
