import {Injectable, Pipe, PipeTransform} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {containerRefreshEnd} from '@angular/core/src/render3';

@Pipe({
  name: 'translateNetworkPipe'
})
@Injectable()
export class TranslateNetworkPipe implements PipeTransform {

  constructor(private sanitizer: DomSanitizer) {

  }

  transform(item: any, field?: string, value?: string): any {
    if (!item) {
      return '?';
    } else {
      let htmlString;

      if (item >= 20) {  // VDSL
        htmlString = this.createRateStar(5, 5);
      }
      if (item >= 7 && item < 20) { // ADSL 2+
        htmlString = this.createRateStar(4, 5);
      }
      if (item >= 1.4 && item < 7) { // ADSL
        htmlString = this.createRateStar(3, 5);
      }
      if (item >= 0.4 && item < 1.4) {
        htmlString = this.createRateStar(2, 5);
      }
      if (item >= 0.1 && item < 0.4) {
        htmlString = this.createRateStar(1, 5);
      }
      if (item < 0.1) {
        htmlString = this.createRateStar(0, 5);
      }
      return this.sanitizer.bypassSecurityTrustHtml(htmlString);
    }
  }

  createRateStar(value: number, maxValue: number) {
    return this.createYellowStar(value) + this.createGrayStar(maxValue - value);
  }

  createYellowStar(n?: number): string {
    if (n !== 0) {
      let result = '<clr-icon shape="star" style="background-color: yellow"></clr-icon>';
      for (let i = 1; i < n; i++) {
        result = result.concat('<clr-icon shape="star"  style="background-color: yellow"></clr-icon>');
      }
      return result;
    } else {
      return '';
    }
  }

  createGrayStar(n?: number): string {
    if (n !== 0) {
      let result = '<clr-icon shape="star "></clr-icon>';
      for (let i = 1; i < n; i++) {
        result = result.concat('<clr-icon shape="star "></clr-icon>');
      }
      return result;
    } else {
      return '';
    }
  }
}
