import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {BBranches} from '../../interfaces/bBraches';
import {BBranchesRequest} from '../../interfaces/bBrachesRequest';
import {HttpClient, HttpParams} from '@angular/common/http';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class BankBranchesService {
  bdata : BBranchesRequest[];
  data : BBranches[];

  rootURL = environment.baseUrl

  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/bankbranch`);
  }

  // get(idVisit: string, skipErrorInterceptor?: boolean) {
  //   return this.http.get<BBranches>(this.getServerUrl(this.basePath) + idVisit, this.getOptions(skipErrorInterceptor));
  // }

  public create(bdata: BBranchesRequest) {
    return this.http.post<BBranchesRequest>(`${this.rootURL}/save/bankbranch`, bdata);
  }


  update(data: BBranches) {
    return this.http.post<BBranches>(`${this.rootURL}/save/bankbranch`, data);
  }

  delete(id: BBranches) {
    return this.http.delete<BBranches>(`${this.rootURL}/delete/bankbranch/${id}`);
  }

  search(any: BBranches){
    this.http.get<BBranches>(`${this.rootURL}/search/bankbranch/${any}`);
  }

  // confirm(data: BBranches, skipErrorInterceptor?: boolean) {
  //   return this.http.post<BBranches>(this.getServerUrl(this.basePath) + 'confirm', data, this.getOptions(skipErrorInterceptor));
  // }
}
