import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {CBooksList} from '../../interfaces/cBooksList';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CCardsIngestion} from '../../interfaces/cCardsIngestion';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CreditCardIngestionService {

  data : CCardsIngestion[];
  rootURL = environment.baseUrl;

  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/creditcard/transaction/ingestion`);
  }

  getCreditCardId(id: number) {
    return this.http.get<CCardsIngestion>(`${this.rootURL}/find/transaction/creditcard/ingestion/` + id);
  }
}
