import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {LogService} from '../../services/log.service';

@Component({
  selector: 'app-check-webcam',
  templateUrl: './check-webcam.component.html',
  styleUrls: ['./check-webcam.component.scss']
})
export class CheckWebcamComponent implements OnInit, OnDestroy {

  private video: HTMLVideoElement;


  constructor(private logService: LogService) {
  }

  ngOnInit() {
    this.video = document.querySelector('#videoElement');


    if (navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices.getUserMedia(
        {video: true, audio: true})
        .then((stream) => {
          this.video.srcObject = stream;
          this.logService.appendLog('INFO', 'Webcam Trovata');
        })
        .catch((err) => {
          this.logService.appendLog('ERROR', 'Errore webcam', err);
        });
    }
  }

  ngOnDestroy(): void {

  }
}
