import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent } from 'ng2-smart-table/ng2-smart-table.component';
import { CCardList } from '../../interfaces/cCardList';
import { CreditCardListService } from '../../services/http/credit-card-list.service';
import { environment } from '../../../../../environments/environment';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-card-list',
  templateUrl: './card-list.component.html',
  styleUrls: ['./card-list.component.scss']
})
export class CardListComponent implements OnInit {

  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  rootURL = environment.baseUrl;
  data: CCardList [];

  settings = {
    actions: {
      edit: false,
      confirmSave:false,
      add: false,
      delete: false,
      position: 'right',
        columnTitle: 'Action'
      },
    columns: {
        id: {
          title: 'Id',
          filter: false,
          editable: false,
          addable: false
        },
        prod: {
          title: 'prod',
          filter: false,
          editable: true,
          addable: false
        },
        codicePan: {
          title: 'numCard',
          filter: false,
          editable: true,
          addable: false
        },
        stato: {
          title: 'stato',
          filter: false,
          editable: true,
          addable: false
        },

        noOfSmartDesk: {
          title: 'Smart Desks',
          filter: false,
          editable: false,
          addable: false,
          type: 'html',
          valuePrepareFunction: (cell, row) => {
            return `<a href="/vr/smart-desks/credit/${row.id}" class="smart_desk_path">${row.noOfSmartDesk}</a>`;
          }
        },
        noOfIngestionTransaction: {
          title: 'Ingestion Transaction',
          filter: false,
          editable: false,
          addable: false,
          type: 'html',
            valuePrepareFunction: (cell, row) => {
              return `<a href="/vr/credit-cards-ingestion/${row.id}" class="smart_desk_path">${row.noOfIngestionTransaction}</a>`;
            }
        },
        noOfLodingTransaction: {
          title: 'Loading Transaction',
          filter: false,
          editable: false,
          addable: false,
          type: 'html',
          valuePrepareFunction: (cell, row) => {
            return `<a href="/vr/credit-cards-loading/${row.id}" class="smart_desk_path">${row.noOfLodingTransaction}</a>`;
            }
        },
        noOfDeliveryTransaction: {
            title: 'Delivery Transaction',
            filter: false,
            editable: false,
            addable: false,
            type: 'html',
            valuePrepareFunction: (cell, row) => {
              return `<a href="/vr/credit-cards-delivery/${row.id}" class="smart_desk_path">${row.noOfDeliveryTransaction}</a>`;
            }
        },
        Action: {
          filter: false,
          editable: false,
          addable: false,
          title: 'Action',
          type: 'html',
          valuePrepareFunction: (cell: any, row: { id: any; noOfSmartDesk: any; }) => {
            return `<a href="/vr/credit-card-view/${row.id}" class="smart_desk_path">View</a>`;
          }
      },
    }
  };

  id:any="";
  constructor(private http: HttpClient,  private route: ActivatedRoute, private creditCardListService: CreditCardListService){
    this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      //alert( this.checkbook+" "+this.id);
      // console.log(this.id);
      if(this.id != null ){this.loadDataBySmartID();}else{this.loadData();}
    })
  }
   // serach funtion
   onSearch(query: string = '') {
    console.log(query);
    if(query != ''){
      this.http.get<CCardList>(this.rootURL+'/search/creditcards/'+query).subscribe(
        res => {
          //console.log(res);
          this.data = res['data'];
        },
        (err: HttpErrorResponse) => {
            if (err.error instanceof Error) {
              console.log("Client-side error occured.");
            } else {
              console.log("Server-side error occured.");
            }
          });
        //console.log(this.actionValue);
        }else{
          this.loadData();
        }
    }

  ngOnChanges(){
    if(this.id != null ){this.loadDataBySmartID();}else{this.loadData();}
  }

  //load all data from card if id not present
  loadData(){
    this.creditCardListService.getAll().subscribe((data : CCardList[]) => {
      this.data = data['data'];
     // console.log(this.data);
    });
  }

  //load data from card if smartdesk id is present
  loadDataBySmartID(){
    this.creditCardListService.getBySmartID(this.id).subscribe(data  => {
      this.data = data['data'];
     // console.log(this.data);
    });
  }

  onCustomAction(event){
   // console.log(event);
  }

  ngOnInit(){
    if(this.id != null ){this.loadDataBySmartID();}else{this.loadData();}
  };

}
