import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import IJanusPluginHandle = JanusStatic.IJanusPluginHandle;
import {LogService} from '../../services/log.service';
import {StatusDetailService} from '../../services/status-detail.service';
import {CALLSTATUS} from '../../enums/callstatus.enum';

@Component({
  selector: 'app-agenda',
  templateUrl: './agenda.component.html',
  styleUrls: ['./agenda.component.scss']
})
export class AgendaComponent implements OnInit {

  @Input() username: string[];

  @Input() janusPluginHandle: IJanusPluginHandle;

  @Input() doCall: (username: string, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) => void;

  @Input() userList: string[];
  @Output() userListChange = new EventEmitter<string[]>();

  constructor(
    private logService: LogService,
    private statusDetailService: StatusDetailService) {
  }

  ngOnInit() {

  }

  getUsers(janusPluginHandle: IJanusPluginHandle) {
    janusPluginHandle.send({
      message: {request: 'list'},
      success: () => {
        this.logService.appendLog('INFO', 'on send msg success : List');
      },
      error: (err) => {
        this.logService.appendLog('ERROR', 'on send msg offer errore', err);
      }
    });
  }

  isMine(user) {
    return user === this.username;
  }
}
