import {Component, Input, OnInit} from '@angular/core';
import {ViewCell} from 'ng2-smart-table';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-visit-datetime-render',
  template: '{{renderValue  | date:"EEE d MMMM  HH:mm"| titlecase }}'
})
export class VisitDatetimeRenderComponent implements ViewCell, OnInit {

  renderValue: string;

  @Input() value: any;
  @Input() rowData: any;

  constructor(private translate: TranslateService) {
  }

  ngOnInit() {
    this.translate.get(
      'vr.common.notDefined'
    ).subscribe(
      result => {
        this.renderValue = this.value;
      });
  }
}
