import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {HttpClient, HttpParams} from '@angular/common/http';
import { CCardsDelivery} from '../../interfaces/cCardsDelivery';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CreditCardDeliveryService {

  data : CCardsDelivery[];
  rootURL = environment.baseUrl
  constructor(private http : HttpClient) {
  }

  getAll() {
       return this.http.get( `${this.rootURL}/find/creditcard/transaction/delivery`);
  }

  getCreditCardId(id: number) {
    return this.http.get<CCardsDelivery>(`${this.rootURL}/find/transaction/creditcard/delivery/` + id);
  }
}
