import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {FormControl} from '@angular/forms';
import {debounceTime} from 'rxjs/operators';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'lib-dynamic-table-search',
  templateUrl: './dynamic-table-search.component.html',
  styleUrls: ['./dynamic-table-search.component.scss']
})
export class DynamicTableSearchComponent implements OnInit {

  @Output() onSearch: EventEmitter<string> = new EventEmitter();

  @Output() onFocus?: EventEmitter<boolean> = new EventEmitter();

  searchField: FormControl = new FormControl();

  constructor() {
  }

  ngOnInit() {
    this.searchField.valueChanges
      .pipe(debounceTime(900))
      .subscribe(term => {
        this.onSearch.emit(this.searchField.value);
      });
  }

  onKeyDownEnter() {
    this.onSearch.emit(this.searchField.value);
  }

  onFocusEmit(b: boolean) {
    if (this.onFocus) {
      this.onFocus.emit(b);
    }
  }

}
