import {Inject, Injectable} from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {IMedia} from '../../interfaces/iMedia';


@Injectable({
  providedIn: 'root'
})
export class MediaService extends HttpCommonService {

  private basePath = '/api/media/';

  upload(data: FormData, skipErrorInterceptor?: boolean) {
    return this.http.post<IMedia>(this.getGatewayUrl(this.basePath) + 'VISIT_IDENTITY_DOCUMENT', data, this.getOptions(skipErrorInterceptor));
  }

  download(mediaId: string, skipErrorInterceptor?: boolean) {
    return this.http.get(this.getGatewayUrl(this.basePath) + mediaId, {responseType: 'arraybuffer'});
  }
}
