import {Component, EventEmitter, Inject, OnDestroy, OnInit, Output} from '@angular/core';
import {StatusDetailService} from '../../services/status-detail.service';
import {Subscription} from 'rxjs';
import {CALLSTATUS} from '../../enums/callstatus.enum';
import {IStatusDetail} from '../../interfaces/iStatusDetail';
import {VrWebSocketService} from '../../services/socket/vr-web-socket.service';
import {WEBSOCKETAGENTSTATUS} from '../../enums/sockets/websocketagentstatus.enum';
import {Person} from 'ngx-uaa/lib/models/person';
import {HttpAuthService, HttpProfileService} from 'ngx-uaa';
import {Router} from '@angular/router';
import {NgxNotificationsService} from 'ngx-notify';
import {CallcenterClientStatusService} from '../../services/callcenter-client-status.service';
import {IClient} from '../../interfaces/iClient';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit, OnDestroy {

  currentUser: Person;
  @Output() onNavItemSelected = new EventEmitter();

  private statusDetailSubscription: Subscription;
  private callcenterClientsStatusSubscription: Subscription;

  currentStatus: IStatusDetail;

  me: IClient;

  CALLSTATUS = CALLSTATUS;
  WEBSOCKETAGENTSTATUS = WEBSOCKETAGENTSTATUS;


  hasPerms = false;

  constructor(private statusDetailService: StatusDetailService,
              private vrWebSocketService: VrWebSocketService,
              private httpProfileService: HttpProfileService,
              private callcenterClientStatusService: CallcenterClientStatusService,
              private ngxNotificationsService: NgxNotificationsService,
              private router: Router,
              private httpAuthService: HttpAuthService) {
  }

  ngOnInit() {
    this.httpProfileService.current().subscribe(
      (response) => {
        this.currentUser = response;

        this.httpProfileService.__canDo('PERSON', 'READ').subscribe((perms) => {
          this.hasPerms = perms as boolean;
        });

        this.callcenterClientsStatusSubscription = this.callcenterClientStatusService.getCallcenterClientsStatusBS().subscribe((next) => {
          if (next) {
            const clientsMap: Map<string, IClient> = next;
            this.me = clientsMap.get(this.currentUser.identity.identity);
          }
        });
      }
    );

    this.statusDetailSubscription = this.statusDetailService.getStatusDetailBS().subscribe(
      next => {
        this.currentStatus = next;
      }
    );
  }

  openMyDataModal() {
    if (this.canNavigate()) {
      this.onNavItemSelected.emit('profile');
    }
  }

  openNetworkingModal() {
    if (this.canNavigate(true)) {
      this.onNavItemSelected.emit('check-network');
    } else {
      const n = navigator as any;
      const str = String()
        .concat('  Downlink: ' + n.connection.downlink + 'Mb/s')        // Effective bandwidth estimate
        .concat('  (' + n.connection.effectiveType + ')')
        .concat('  RTT: ' + n.connection.rtt + 'ms\n'); // Effective round-trip time estimate
      const msg = {title: 'Stima connessione', body: str};
      switch (n.connection.effectiveType) {
        case '4g': {
          this.ngxNotificationsService.success(msg);
          break;
        }
        case '3g': {
          this.ngxNotificationsService.warn(msg);
          break;
        }
        default: {
          this.ngxNotificationsService.error(msg);
          break;
        }
      }
    }
  }

  openPersonModal() {
    if (this.canNavigate()) {
      this.onNavItemSelected.emit('person');
    }
  }

  onChangeStatus(agentStatus: WEBSOCKETAGENTSTATUS) {
    if (this.canNavigate()) {
      this.vrWebSocketService.sendAgentStatus(this.currentUser.identity.identity, agentStatus);
    }
  }

  onNavigate(routerLink: string) {
    if (this.canNavigate()) {
      this.router.navigateByUrl(routerLink);
    }
  }

  onLogout() {
    if (this.canNavigate()) {
      this.httpAuthService.logout(true).subscribe(
        response => {
          location.reload();
        },
        error => {
          this.ngxNotificationsService.error({body: 'account.logout.error'});
        }
      );
    }
  }

  getClassFromStatus(agentStatus: WEBSOCKETAGENTSTATUS) {
    const myclass = 'is-solid';
    switch (agentStatus) {
      case WEBSOCKETAGENTSTATUS.FREE: {
        return myclass + ' has-badge--success';
        break;
      }
      case WEBSOCKETAGENTSTATUS.BUSY: {
        return myclass + ' has-badge--danger';
        break;
      }
      case WEBSOCKETAGENTSTATUS.PAUSE: {
        return myclass + ' has-alert';
        break;
      }
    }
  }

  private canNavigate(notNotify?: boolean) {
    if (this.currentStatus.code !== CALLSTATUS.CALL) {
      return true;
    } else {
      if (!notNotify) {
        this.ngxNotificationsService.warn({
          body: 'vr.common.navigation_error'
        });
      }
      return false;

    }
  }

  ngOnDestroy(): void {
    try {
      this.statusDetailSubscription.unsubscribe();
      this.callcenterClientsStatusSubscription.unsubscribe();
    } catch (e) {
      this.statusDetailSubscription = null;
      this.callcenterClientsStatusSubscription = null;
    }
  }
}
