import {Component, Input, OnChanges, OnInit, SimpleChange, SimpleChanges, ViewChild} from '@angular/core';
import {VisitService} from '../../services/http/visit.service';

import {IPageable} from 'ngx-uaa/lib/interfaces/iPageable';
import {Ng2SmartTableComponent} from 'ng2-smart-table/ng2-smart-table.component';
import {LocalDataSource} from 'ng2-smart-table';
import {IPagedData} from 'ngx-uaa/lib/interfaces/iPagedData';
import {IVisitSearchRequest} from '../../interfaces/iVisitSearchRequest';
import {CtrlTypesEnum} from 'ngx-json-form';
import {LangChangeEvent, TranslateService} from '@ngx-translate/core';
import {VisitIconRenderComponent} from '../dynamic-table/dynamic-table-render-components/visit-icon-render-component/visit-icon-render-component';
import {VisitDatetimeRenderComponent} from '../dynamic-table/dynamic-table-render-components/visit-datetime-render/visit-datetime-render.component';
import {VisitVisitorsRenderComponent} from '../dynamic-table/dynamic-table-render-components/visit-visitors-render/visit-visitors-render.component';
import {VisitCallRequestTotemRenderComponent} from '../dynamic-table/dynamic-table-render-components/visit-call-request-totem-render/visit-call-request-totem-render.component';
import {IVisit} from '../../interfaces/iVisit';
import {VisitStatusRenderComponent} from '../dynamic-table/dynamic-table-render-components/visit-status-render-component/visit-status-render-component';

import * as momentTimezoneImported from 'moment-timezone';
import {NullableValueRenderComponent} from '../dynamic-table/dynamic-table-render-components/nullable-value-render/nullable-value-render.component';

const momentTimezone = momentTimezoneImported;


@Component({
  entryComponents: [VisitIconRenderComponent, VisitDatetimeRenderComponent, VisitCallRequestTotemRenderComponent, VisitVisitorsRenderComponent, VisitStatusRenderComponent, NullableValueRenderComponent],
  selector: 'app-visit-list',
  templateUrl: './visit-list.component.html',
  styleUrls: ['./visit-list.component.scss']
})
export class VisitListComponent implements OnInit, OnChanges {

  @Input('refresh') refresh?: boolean;

  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;

  openUserCheckin = false;

  showAdvancedSearch = false;

  settings = {
    editable: false,
    noDataMessage: 'Nessun dato da visualizzare',
    actions: {
      edit: false,
      add: false,
      delete: false,
      custom: [
        {name: 'edit', title: '<a> Dettagli </a>'},
        /*        {name: 'delete', title: '<a> Elimina </a>'}*/
      ],
      position: 'right',
      columnTitle: 'Azioni'
    },
    columns: {
      icon: {
        filter: false,
        title: '',
        type: 'custom',
        renderComponent: VisitIconRenderComponent,
      },
      startDate: {
        filter: false,
        title: 'startDate',
        type: 'custom',
        renderComponent: VisitDatetimeRenderComponent,
      },
      endDate: {
        filter: false,
        title: 'endDate',
        type: 'custom',
        renderComponent: VisitDatetimeRenderComponent,
      },
      note: {
        filter: false,
        title: 'note',
        type: 'custom',
        renderComponent: NullableValueRenderComponent
      },
      locked: {
        filter: false,
        title: 'locked',
        type: 'custom',
        renderComponent: VisitStatusRenderComponent,
      },
      visitors: {
        filter: false,
        title: 'visitors',
        type: 'custom',
        renderComponent: VisitVisitorsRenderComponent,
      },
      callRequest: {
        filter: false,
        title: 'callRequest',
        type: 'custom',
        renderComponent: VisitCallRequestTotemRenderComponent,
      }
    }
  };

  data: LocalDataSource = new LocalDataSource();  // handler dati tabella

  pageable: IPageable = {
//    sort: 'dateFrom,asc',
    size: 10,
    page: 0
  }; // richiesta pageable;

  page: IPagedData = {
    number: 0
  };  // l'ultima pagina ricevuta.

  search: IVisitSearchRequest = {
    dateFrom: momentTimezone.tz('Europe/Rome').set({hour: 0, minute: 0, second: 0, millisecond: 0}).format(), //.subtract(1, 'days').format(),
    dateTo: momentTimezone.tz('Europe/Rome').set({hour: 23, minute: 59, second: 59, millisecond: 999}).format(),
    stringFilter: null
  };

  searchForm = JSON.stringify({
    name: 'Search',
    showTitle: false,
    excludeForEmit: ['closed'],
    actions: [],
    elements: [
      {
        name: 'orario',

        ctrltype: CtrlTypesEnum.DATETIMERANGE,
        disabled: false,

        validations: [
          {type: 'testdatatimerangenostartdate', errMsg: 'Occorre inserire un valore per la \'Data di ingresso\''},
          {type: 'testdatatimerangenostarttime', errMsg: 'Occorre inserire un valore per \'Orario di ingresso\''},
          {type: 'testdatatimerangenoenddate', errMsg: 'Occorre inserire un valore per la \'Data di uscita\''},
          {type: 'testdatatimerangenoendttime', errMsg: 'Occorre inserire un valore per \'Orario di uscita\''},
          {type: 'testdatatimerangemin', errMsg: 'Occorre inserire una data uguale o superiore a quella corrente per la data di ingresso'},
          /*                    {type: 'testdatatimerangemax', errMsg: 'Occorre inserire una data minore o uguale al \'30/12/2018\' per la data di uscita'},*/
          {type: 'testdatatimerangenotvalid', errMsg: 'La data di ingresso deve essere minore o uguale alla data di uscita'}
        ],
        formState: 'opened',
        groupElChilds: [
          {
            label: 'Data Ingresso',
            value: momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD') //.subtract(1, 'days').format('YYYY-MM-DD')
          },
          {
            label: 'Ora',
            value: '00:00'//momentTimezone.tz('Europe/Rome').format('HH:mm')
          },
          {
            label: 'Data Uscita',
            value: momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD')
          },
          {
            label: 'Ora',
            value: '23:59'//momentTimezone.tz('Europe/Rome').format('HH:mm')
          },
        ]
      },
    ]
  });

  selectedVisit: IVisit;

  lastSearchTimestamp: number;
  dataDebounceTimeout;

  constructor(private translate: TranslateService, private visitService: VisitService) {
    this.data.onChanged().subscribe(this.onSmartTableEvent);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.refresh.currentValue === true) {
      this.loadData();
    }
  }

  ngOnInit() {
    this.data.empty(); // inizializzazione

    setTimeout(() => {
      this.setTranslation();
    }, 1000);
    this.loadData();

    this.smartTable.custom.subscribe((dataObject: any) => {
      switch (dataObject.action) {
        case 'delete': {
          //  this.onDelete(dataObject);
          break;
        }
        case 'edit': {
          //   this.onEdit(dataObject);
          break;
        }
      }
    });

    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      this.setTranslation();
    });
  }

  onValuesChange(event) {
    const values = event.srcCtrl.value;
    if (values.startDate && values.startTime) {
      this.search.dateFrom = momentTimezone.tz(values.startDate + ' ' + values.startTime, 'YYYY-MM-DD, hh:mm', 'Europe/Rome').format();
    }
    if (values.endDate && values.endTime) {
      this.search.dateTo = momentTimezone.tz(values.endDate + ' ' + values.endTime, 'YYYY-MM-DD, hh:mm', 'Europe/Rome').format();
    }
    clearTimeout(this.dataDebounceTimeout);
    this.dataDebounceTimeout = setTimeout(() => {
      this.loadData(0);
    }, 800);
  }

  onSmartTableEvent = (event) => {
    switch (event.action) {
      case 'sort': {
        const sortConfigs = this.data.getSort()[0];
        this.pageable.sort = sortConfigs.field + ',' + sortConfigs.direction;
        this.loadData(0);
        break;
      }
    }
  };

  loadData(page?: number) {
    this.pageable.page = (page || page === 0) ? page : this.pageable.page;

    const searchTimestamp = (new Date()).getTime();
    this.lastSearchTimestamp = searchTimestamp;

    this.visitService.search(this.pageable, this.search, true).subscribe(
      pagedData => {
        if (searchTimestamp === this.lastSearchTimestamp) { // ACCETTO SOLO L'ULTIMA RISPOSTA
          this.page = pagedData;
          this.data.setPaging(this.page.number, this.page.size);

          this.data.load(this.page.content).then(() => {
            this.data.refresh();
          });
        }
      });
  }

  onSearch(q) {
    this.search.stringFilter = q;
    this.loadData(0);
  }

  onFocus(isFocused: boolean) {
    if (isFocused === true) {
      this.showAdvancedSearch = isFocused;
    }
  }

  onCustomAction(row) {
    switch (row.action) {
      case 'edit': {
        this.selectedVisit = row.data as IVisit;
        this.openUserCheckin = true;
        break;
      }
      case 'delete': {
        const visitToDelete: IVisit = row.data as IVisit;
        this.visitService.delete(visitToDelete).subscribe((response) => {

        });
        break;
      }
    }

  }

  setTranslation() {
    const b = 'vr.visit.';
    const a = [
      b + 'icon',
      b + 'startDate',
      b + 'endDate',
      b + 'note',
      b + 'locked',
      b + 'visitors',
      b + 'callRequest',
      'vr.common.noDataMessage'
    ];
    this.translate.get(a).subscribe(
      result => {
        this.settings.columns.icon.title = result[b + 'icon'];
        this.settings.columns.startDate.title = result[b + 'startDate'];
        this.settings.columns.endDate.title = result[b + 'endDate'];
        this.settings.columns.note.title = result[b + 'note'];
        this.settings.columns.locked.title = result[b + 'locked'];
        this.settings.columns.visitors.title = result[b + 'visitors'];
        this.settings.columns.callRequest.title = result[b + 'callRequest'];
        this.settings.noDataMessage = result['vr.common.noDataMessage'];
        this.settings = Object.assign({}, this.settings);
      }
    );
  }

  onUserModalCloseEvent($event: boolean) {
    if ($event === false) {
      this.loadData();
    }
  }
}
