import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent } from 'ng2-smart-table/ng2-smart-table.component';
import { CCardsIngestion} from '../../interfaces/cCardsIngestion';
import { CreditCardIngestionService } from '../../services/http/credit-card-ingestion.service';
import { environment } from '../../../../../environments/environment';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-credit-card-ingestion',
  templateUrl: './credit-card-ingestion.component.html',
  styleUrls: ['./credit-card-ingestion.component.scss']
})
export class CreditCardIngestionComponent implements OnInit {

  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  displaySearch = true;
  data: CCardsIngestion [];
  rootURL = environment.baseUrl;
  settings = {
    actions: {
      edit: false,
      add: false,
      delete: false,
      position: 'right',
        columnTitle: 'Action'
      },

    columns: {
        id: {
          title: 'Id',
          filter: false,
          editable: true,
          addable: false
        },
        status: {
          title: 'status',
          filter: false,
          editable: true,
          addable: false
        },
        bankOperatorId: {
          title: 'bankOperatorId',
          filter: false,
          editable: true,
          addable: false
        },
        creditCardId: {
          title: 'chequeBookId',
          filter: false,
          editable: true,
          addable: false
        }
      }
  };

  id:any="";
  constructor(private http: HttpClient, private route: ActivatedRoute,  private ccIngestionService: CreditCardIngestionService){
    this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      console.log(this.id);
      if(this.id != null){this.loadDataByCCId(); this.displaySearch=false;}else{this.loadData();}
    });
  }

  onSearch(query: string = '') {
    console.log(query);
    if(query != ''){
      this.http.get<CCardsIngestion>(this.rootURL+'/search/transaction/creditcard/ingestion/'+query).subscribe(
        res => {
          console.log(res);
          this.data = res['data'];
        },
        (err: HttpErrorResponse) => {
            if (err.error instanceof Error) {
              console.log("Client-side error occured.");
            } else {
              console.log("Server-side error occured.");
            }
          });
        //console.log(this.actionValue);
        }else{
          this.loadData();
      }
    }

  ngOnChanges(){
    //this.loadData();
  }

  loadData(){
    this.ccIngestionService.getAll().subscribe((data : CCardsIngestion[]) => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  //Load data by chequebookid
  loadDataByCCId(){
    this.ccIngestionService.getCreditCardId(this.id).subscribe(data  => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  ngOnInit(){
    //this.loadData();
  };

}
