import { HttpClient, HttpErrorResponse, HttpHeaders  } from '@angular/common/http';
import { Component, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LocalDataSource } from 'ng2-smart-table';
import { Ng2SmartTableComponent} from 'ng2-smart-table/ng2-smart-table.component';
import { SDesks} from '../../interfaces/sDesks';
import { SDesksCreate} from '../../interfaces/sDesksCreate';
import { SmartDesksService } from '../../services/http/smart-desks.service';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-smart-desks',
  templateUrl: './smart-desks.component.html',
  styleUrls: ['./smart-desks.component.scss']
})
export class SmartDesksComponent implements OnInit {

  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  rootURL = environment.baseUrl;
  errmsg:boolean = false;
  display_msg:any="";
  settings = {
    add:{
      confirmCreate:true
    },
    edit:{
      confirmSave:true
    },
    delete :{
      confirmDelete: true
    },
    actions: {
      position: 'right',
      columnTitle: 'Action',
    },

    columns: {
      id: {
          title: 'Id',
          filter: false,
          editable: false,
          addable: false
        },
        smartDeskName: {
          filter: false,
          title: 'Desk Name'
        },
        externalId: {
          filter: false,
          title: 'External Id'
        },
        noOfChequeBooks: {
          filter: false,
          editable: false,
          addable: false,
          title: 'Chequebooks',
          type: 'html',
          valuePrepareFunction: (cell, row) => {
            return `<a href="/vr/cheque-books/${row.id}" class="smart_desk_path">${row.noOfChequeBooks}</a>`;
          }

        },
        noOfCards: {
          filter: false,
          editable: false,
          addable: false,
          title: 'Cards',
          type: 'html',
          valuePrepareFunction: (cell, row) => {
            return `<a href="/vr/card-list/${row.id}" class="smart_desk_path">${row.noOfCards}</a>`;
          }

        },
    }
  };

  data: SDesks[];
  id:any;
  checkbook:any;
  credit:any;
  constructor(private http: HttpClient, private route: ActivatedRoute, private smartDesksService: SmartDesksService){
    this.route.paramMap.subscribe(params => {
      this.checkbook = params.get('checkbook');
      this.credit = params.get('credit');
      this.id = params.get('id');
      //alert( this.checkbook+" "+this.id);
      // console.log(this.id);
      if(this.id == null && this.checkbook == null){this.loadAllData();}
      if(this.id != null && this.checkbook == null){this.loadData();}
      if(this.id != null && this.checkbook == "checkbook"){ this.loadDataByCBId();}
      if(this.id != null && this.checkbook == "credit"){  this.loadDataByCCId();}

    });
  }

// Add record to table
addRecord(event) {
  console.log(event);
  var  sdata ={
                "smartDeskName" : event.newData.smartDeskName,
                "externalId" : event.newData.externalId,
                "bankBranchId":this.id,
              };
   // console.log(sdata);
   if(event.newData.smartDeskName !="" &&  event.newData.externalId != "" && !isNaN(event.newData.externalId)) {
       this.smartDesksService.create(sdata).subscribe(
      res => {
        console.log(res);
        event.confirm.resolve(event.newData);
        this.loadData();
    },
    (err: HttpErrorResponse) => {
      if (err.error instanceof Error) {
        console.log("Client-side error occured.");
      } else {
        console.log("Server-side error occured.");
      }
    });
  }else{
    //alert("All fields are required !");
    this.errmsg = true;
    this.display_msg="All fields are required & external_Id must be Number !";
      setTimeout (() => {
        this.errmsg = false;
    }, 2500);
  }

}


 // update record in table
 updateRecord(event: { newData: { id: any; branchName: any; externalId: any; smartDeskName: any}; confirm: { resolve: (arg0: any) => void; }; }) {
  //console.log(event);
  var udata = {
              "id" : event.newData.id,
              "smartDeskName" : event.newData.smartDeskName,
              "externalId" : event.newData.externalId,
              "bankBranchId":this.id,
              };
              //console.log(data);
    if(event.newData.smartDeskName !="" &&  event.newData.externalId != "" && !isNaN(event.newData.externalId)) {
      this.smartDesksService.update(udata).subscribe(
      res => {
       // console.log(res);
        event.confirm.resolve(event.newData);
        this.loadData();
    },
    (err: HttpErrorResponse) => {
      if (err.error instanceof Error) {
        console.log("Client-side error occured.");
      } else {
        console.log("Server-side error occured.");
      }
    });
  }else{
    //alert("All fields are required !");
    this.errmsg = true;
    this.display_msg="All fields are required & external_Id must be Number !";
      setTimeout (() => {
        this.errmsg = false;
    }, 2500);
  }
}

// delete records
deleteRecord(event: any){
  console.log(event.data.smartDeskId);
  if (window.confirm('Are you sure you want to delete?')) {
    event.confirm.resolve();
      this.smartDesksService.delete(event.data.smartDeskId).subscribe(
      res => {
       // console.log(res);
        event.confirm.resolve(event.newData);
    },
    (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          console.log("Client-side error occured.");
        } else {
          console.log("Server-side error occured.");
        }
      });
    } else {
      event.confirm.reject();
    }
  }

  // serach funtion
  onSearch(query: string = '') {
    console.log(query);
    if(query != ''){
      this.http.get<SDesks>(this.rootURL+'/search/smartdesks/'+query).subscribe(
        res => {
          //console.log(res);
          this.data = res['data'];
        },
        (err: HttpErrorResponse) => {
            if (err.error instanceof Error) {
              console.log("Client-side error occured.");
            } else {
              console.log("Server-side error occured.");
            }
          });
        //console.log(this.actionValue);
        }else{
          if(this.id == null){this.loadAllData();}else{this.loadData();}
        }

    }

    // get data from id
    loadData(){
      this.smartDesksService.get(this.id).subscribe(data => {
        this.data = data['data'];
        console.log(this.data);
      });
    }

    // get all data if id is no present
    loadAllData(){
      this.smartDesksService.getAll().subscribe(data => {
        this.data = data['data'];
        console.log(this.data);
      });
    }

  ngOnChanges(){}

  ngOnInit(){
    this.id=this.route.snapshot.paramMap.get("id");
    console.log(this.id);
    // alert(this.id);
  }

   //Load data by chequebookid
   loadDataByCBId(){
    this.smartDesksService.getCheckBookId(this.id).subscribe(data  => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  //Load data by credit card id
  loadDataByCCId(){
    this.smartDesksService.getCreditCardId(this.id).subscribe(data  => {
      this.data = data['data'];
      console.log(this.data);
    });
  }


}
