// animations.ts

import {animate, state, style, transition, trigger} from '@angular/animations';

export const fadeTrigger = [
  trigger('fadeTrigger', [
    state('true', style({display: 'visible', opacity: '1'})),
    state('false', style({display: 'none', opacity: '0'})),
    transition('* => *', animate(10000))
  ])
];

