import {Component, OnDestroy, OnInit} from '@angular/core';
import * as Janus from 'janus-typescript-client';
import IJanusPluginOptions = JanusStatic.IJanusPluginOptions;
import IJanusPluginHandle = JanusStatic.IJanusPluginHandle;
import {BehaviorSubject} from 'rxjs';
import {CALLSTATUS} from '../../enums/callstatus.enum';
import {StatusDetailService} from '../../services/status-detail.service';
import {IStatusDetail} from '../../interfaces/iStatusDetail';
import {LogService} from '../../services/log.service';
import {JanusService} from '../../services/janus.service';
import {fadeTrigger} from '../../../../animations/fade.animation';
import {VIDEOQUALITY} from '../../enums/videoquality.enum';


@Component({
  selector: 'app-video-call',
  templateUrl: './video-call.component.html',
  styleUrls: ['./video-call.component.scss'],
  animations: [fadeTrigger]
})
export class VideoCallComponent implements OnInit, OnDestroy {

  private readonly username = Math.floor((Math.random() * (100 - 0) + 0)).toString();

  private userList: string[] = [];

  // Monitoro lo stato della chiamata, il servizio mi restituisce il json per il template
  CALLSTATUS = CALLSTATUS;
  statusDetail: IStatusDetail;
  statusDetailBS: BehaviorSubject<IStatusDetail>;

  // Servizio di log
  logsBS: BehaviorSubject<any>;
  logs: any [];

  // Oggetti Janus
  private readonly JANUS_PLUGIN_NAME = 'janus.plugin.videocall';
  janusPluginHandle: IJanusPluginHandle;
  private injsep: any;

  // Riferimenti agli elementi del dom. TODO recuperarli con viewchild
  private video: HTMLVideoElement;
  private remoteVideo: HTMLVideoElement;
  private audio: HTMLAudioElement;

  // Configurazioni Janus
  private janusVideoCallPluginOptions: IJanusPluginOptions = {
    plugin: this.JANUS_PLUGIN_NAME,
    success: (janusPluginHandle: IJanusPluginHandle) => { // Plugin attached! 'janusPluginHandle' is our handle
      this.logService.appendLog('INFO', 'janusPluginHandle', janusPluginHandle);

      this.janusPluginHandle = janusPluginHandle;
      this.janusService.setJanusPluginHandle(this.JANUS_PLUGIN_NAME, this.janusPluginHandle);

      this.registerUser(this.username, this.janusPluginHandle);
    },
    error: (cause) => { // Couldn't attach to the plugin
      this.logService.appendLog('ERROR', 'janusPluginHandle', cause);
    },
    consentDialog: (on) => {
      // e.g., Darken the screen if on=true (getUserMedia incoming), restore it otherwise
    },
    onmessage: (msg: any, jsep) => {       // We got a message/event (msg) from the plugin. If jsep is not null, this involves a WebRTC negotiation
      if (msg.error) {
        this.logService.appendLog('ERROR', 'message', msg);
        switch (msg.error_code) {
          case 476: { // L'utente si  gi registrato, dobbiamo aspettare il detach lato server
            const timeWindow = 1000 + Math.floor((Math.random() * (1000 - 0) + 0));
            setTimeout(() => {
              this.ngOnInit();
            }, timeWindow);
            break;
          }
          case 478: { // L'utente chiamato non  registrato
            this.doHangup(this.janusPluginHandle, this.statusDetailService);
            break;
          }
        }
      } else {
        this.logService.appendLog('INFO', 'message', msg);

        // Ho richiesto la lista degli utenti registrati
        if (msg.result.list) {
          this.userList = msg.result.list;
        } else {
          switch (msg.result.event) {
            case 'registered': { // L'utente si  registrato
              this.statusDetailService.setStatus(CALLSTATUS.FREE, this.username);
              this.playSound('../../../../../assets/wav/signin.wav');

              break;
            }
            case 'incomingcall': {
              this.statusDetailService.setStatus(CALLSTATUS.RINGING, this.username, msg.result.username);
              this.playSound('../../../../../assets/wav/ring.wav', true);

              this.injsep = jsep;
              break;
            }
            case 'calling': {
              this.statusDetailService.setStatus(CALLSTATUS.CALLING);
              this.playSound('../../../../../assets/wav/tone.wav', true);

              break;
            }
            case 'accepted': { // L'utente ha accettato
              this.statusDetailService.setStatus(CALLSTATUS.CALL);
              this.playSound('../../../../../assets/wav/dialup.wav');

              /*this.remoteUsername = msg.result.username;*/
              this.janusPluginHandle.handleRemoteJsep({jsep});
              break;
            }
            case 'hangup': {
              switch (msg.result.reason) {
                case 'User busy': {
                  this.statusDetailService.setStatus(CALLSTATUS.BUSY, this.username);
                  this.playSound('../../../../../assets/wav/busy.wav');
                  setTimeout(() => {
                    this.stopSound();
                    this.statusDetailService.setStatus(CALLSTATUS.FREE, this.username);
                  }, 5000);
                  break;
                }
                default : { // CASE : Explicit hangup CASE : We did the hangup CASE : Remote WebRTC hangup
                  this.playSound('../../../../../assets/wav/dialup.wav');
                  this.statusDetailService.setStatus(CALLSTATUS.HANGUP);
                  setTimeout(() => {
                    this.statusDetailService.setStatus(CALLSTATUS.FREE, this.username);
                  }, 3000);
                  break;
                }
              }
              break;
            }
            case 'update': {
              this.logService.appendLog('INFO', 'update jsep', jsep);
              if (jsep) {
                if (jsep.type === 'answer') {
                  this.janusPluginHandle.handleRemoteJsep({jsep: jsep});
                } else {
                  this.janusPluginHandle.createAnswer(
                    {
                      jsep: jsep,
                      media: {data: true},	// Let's negotiate data channels as well
                      success: (jsep) => {
                        const body = {'request': 'set'};
                        this.janusPluginHandle.send({'message': body, 'jsep': jsep});
                      },
                      error: (error) => {

                      }
                    });
                }
              }
              break;
            }
          }
        }
      }
    },
    onlocalstream: (stream) => {       // We have a local stream (getUserMedia worked!) to display
      if (this.video) {
        Janus.attachMediaStream(this.video, stream);
        this.logService.appendLog('INFO', 'on local stream :', stream);
      } else {
        this.logService.appendLog('ERROR', 'on local stream : DOM video element not found');
      }
    },
    onremotestream: (stream) => {       // We have a remote stream (working PeerConnection!) to display
      if (this.remoteVideo) {
        Janus.attachMediaStream(this.remoteVideo, stream);
        this.logService.appendLog('INFO', 'on remote stream :', stream);
      } else {
        this.logService.appendLog('ERROR', 'on remote stream : DOM video element not found');
      }
    },
    oncleanup: () => {      // PeerConnection with the plugin closed, clean the UI. The plugin handle is still valid so we can create a new one
      this.logService.appendLog('INFO', 'on cleanup');
    },
    detached: () => {       // Connection with the plugin closed, get rid of its features. The plugin handle is not valid anymore
      this.logService.appendLog('INFO', 'detached');
    }
  };

  constructor(private janusService: JanusService, private statusDetailService: StatusDetailService, private logService: LogService) {
    this.logsBS = logService.getLogBS();

    this.statusDetailBS = statusDetailService.getStatusDetailBS();
    this.statusDetailBS.subscribe((next: IStatusDetail) => {
      this.statusDetail = next;
    });
  }

  ngOnInit() {
    this.video = document.querySelector('#videoElement');
    this.remoteVideo = document.querySelector('#remoteVideoElement');

    if (!this.video || !this.remoteVideo) {
      if (!this.video) {
        this.logService.appendLog('ERROR', 'document.querySelector(\'#videoElement\')');
      } else {
        this.logService.appendLog('ERROR', 'document.querySelector(\'#remoteVideoElement\')');
      }
    } else {

      this.janusService.detachJanusPlugin(this.JANUS_PLUGIN_NAME);
      this.janusService.createJanusPluginHandle(this.JANUS_PLUGIN_NAME,
        this.janusVideoCallPluginOptions,
        () => {
          this.statusDetailService.setStatus(CALLSTATUS.NOT_REGISTRED);
        }, (err) => {
          this.logService.appendLog('ERROR', 'on janus plugin creation', err);
          this.statusDetailService.setStatus(CALLSTATUS.ERROR);
        }
      );
    }
  }

  stopSound() {
    if (this.audio) {
      if (this.audio.pause) {
        this.audio.pause();
      }
      if (this.audio.remove) {
        this.audio.remove();
      }
    }
  }

  playSound(src: string, loop?: boolean) {
    this.stopSound();
    this.audio = new Audio();
    this.audio.src = src;
    this.audio.load();
    this.audio.loop = loop || false;
    const playedPromise = this.audio.play();
    if (playedPromise) {
      playedPromise
        .then(() => {

        })
        .catch((e) => {
/*          this.logService.appendLog('ERROR', 'errore riproduzione suono', e);*/
          if (e.name === 'NotAllowedError' ||
            e.name === 'NotSupportedError') {
/*            this.logService.appendLog('ERROR', 'errore riproduzione suono', e);*/
          }
        });
    }
  }

  registerUser(username: string, janusPluginHandle: IJanusPluginHandle) {
    janusPluginHandle.send({

      message: {
        request: 'register',
        username: ''.concat(username)
      },

      success: () => {
        this.logService.appendLog('SUCCESS', 'on send msg success : Register');
      },
      error: (err) => {
        this.logService.appendLog('SUCCESS', 'on send msg error : Register', err);
      }
    });
  }

  doCall(username: string, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    statusDetailService.setStatus(CALLSTATUS.CALLING);
    janusPluginHandle.createOffer(
      {
        media: {   // By default, it's sendrecv for audio and video...
          data: true,
          video: VIDEOQUALITY.STANDARD_WIDE,
        },
        success: (jsep) => {
          const body = {request: 'call', username};
          janusPluginHandle.send({message: body, jsep});
        },
        error: (error) => {
        }
      });
  }

  doHangup(janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    statusDetailService.setStatus(CALLSTATUS.HANGUP);
    // Hangup a call
    const hangup = {request: 'hangup'};
    janusPluginHandle.send({message: hangup});
    janusPluginHandle.hangup();
  }

  doResponse(injsep, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    statusDetailService.setStatus(CALLSTATUS.CALL);
    janusPluginHandle.createAnswer(
      {
        jsep: injsep,
        // No media provided: by default, it's sendrecv for audio and video
        media: {
          video: VIDEOQUALITY.STANDARD_WIDE,
          data: true
        },
        success: (jsep) => {
          const body = {request: 'accept'};
          janusPluginHandle.send({message: body, jsep});
        },
        error: (error) => {
          this.logService.appendLog('ERROR', 'create Answer error', error);
        }
      });

  }

  // mostra i log in caso di errore
  showLogs() {
    if (this.logs) {
      this.logs = undefined;
    } else {
      this.logs = [];
      const subscription = this.logsBS.subscribe((next: any) => {
        this.logs.push(next);
      });
      subscription.unsubscribe();
    }
  }

  ngOnDestroy(): void {
    this.janusService.detachJanusPlugin(this.JANUS_PLUGIN_NAME);
  }
}
