import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CCardList } from '../../interfaces/cCardList';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CreditCardListService {

  data : CCardList[];
  rootURL = environment.baseUrl;

  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/creditcards`);
  }

  get(id: number) {
    return this.http.get<CCardList>(`${this.rootURL}/find/creditcard/byid/` + id);
  }

  getBySmartID(id: number) {
    return this.http.get<CCardList>(`${this.rootURL}/find/creditcard/bysmartdeskid/` + id);
  }

  // public create(bdata: BBranchesRequest) {
  //   return this.http.post<BBranchesRequest>(`${this.rootURL}/save/bankbranch`, bdata);
  // }

  update(data: CCardList) {
    return this.http.post<CCardList>(`${this.rootURL}/ingestion/creditcards`, data);
  }

  delete(id: CCardList) {
    return this.http.delete<CCardList>(`${this.rootURL}/delete/bankbranch/${id}`);
  }

  search(any: CCardList){
    this.http.get<CCardList>(`${this.rootURL}/search/bankbranch/${any}`);
  }

}
