import {Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {VisitService} from '../../services/http/visit.service';
import {IVisitCreateRequest} from '../../interfaces/iVisitCreateRequest';
import {CtrlTypesEnum, CtrlValueTypeEnum} from 'ngx-json-form';
import {ClrWizard, ClrWizardPage} from '@clr/angular';
import {VisitorService} from '../../services/http/visitor.service';
import {MediaService} from '../../services/http/media.service';
import {IVisitor} from '../../interfaces/iVisitor';
import {IVisit} from '../../interfaces/iVisit';
import {IVisitorCreateRequest} from '../../interfaces/iVisitorCreateRequest';
import {IMedia} from '../../interfaces/iMedia';
import {IVisitorAttachmentRequest} from '../../interfaces/iVisitorAttachmentRequest';
import {ModalContent, NgxNotificationsService} from 'ngx-notify';
import {VrWebSocketService} from '../../services/socket/vr-web-socket.service';

import * as momentImported from 'moment';

const moment = momentImported;

import * as momentTimezoneImported from 'moment-timezone';
import {VisitorsGetmediaRenderComponent} from '../dynamic-table/dynamic-table-render-components/visitors-getmedia-render/visitors-getmedia-render.component';
import {forkJoin, Subject} from 'rxjs';
import {StatusDetailService} from '../../services/status-detail.service';
import {CALLSTATUS} from '../../enums/callstatus.enum';
import 'rxjs-compat/add/operator/finally';
import {VisitorsSolariResponseRenderComponent} from '../dynamic-table/dynamic-table-render-components/visitors-solari-response-render/visitors-solari-response-render.component';

const momentTimezone = momentTimezoneImported;

@Component({
  entryComponents: [VisitorsGetmediaRenderComponent,VisitorsSolariResponseRenderComponent],
  selector: 'app-user-check-in',
  templateUrl: './user-check-in.component.html',
  styleUrls: ['./user-check-in.component.scss']
})
export class UserCheckInComponent implements OnInit, OnDestroy {

  @Input('hiddenToggle') hiddenToggle?: boolean;      // nascondere il pulsante checkin
  @Input('open') open ?: boolean;                             // modal aperta/chiusa
  @Output('openChange') openChange ?: EventEmitter<boolean> = new EventEmitter();
  @Input('callId') private callId: number;
  @Input('visit') private visit?: IVisit; // CASO EDIT: passo la visita

  @Input('videoUser') private videoUser?: ElementRef;
  @Input('videoScanner') private videoScanner?: ElementRef;
  @Input('videoReverseOrdering') private videoReverseOrdering?: boolean;

  @ViewChild('wizard') wizard: ClrWizard;
  @ViewChild('pageNewVisit') pageNewVisit: ClrWizardPage;
  @ViewChild('pageVisitors') pageVisitors: ClrWizardPage;
  @ViewChild('pageSummary') pageSummary: ClrWizardPage;

  canvasUser: FormData = new FormData();
  canvasDocFront: FormData = new FormData();
  canvasDocRetro: FormData = new FormData();

  acquiredUser: false;
  acquiredDocFront: false;
  acquiredDocRetro: false;

  checkInForm = new FormGroup({
    firstName: new FormControl('', Validators.required),
    lastName: new FormControl('', Validators.required),
  });

  formContent = '';

  /*  newVisitorForm = JSON.stringify({});*/
  newVisitor: IVisitor;
  settings = {
    hideSubHeader: true,
    mode: 'inline',
    noDataMessage: 'Nessun utente inserito',
    delete: {
      confirmDelete: true,
      deleteButtonContent: 'Elimina'
    },
    actions: {
      edit: false,
      delete: true,
      position: 'right',
      columnTitle: 'Azioni'
    },
    columns: {
      solariResponse: {
        title: 'Stato',
        editable: false,
        type: 'custom',
        renderComponent: VisitorsSolariResponseRenderComponent,
      },
      name: {
        title: 'Nome'
      },
      surname: {
        title: 'Cognome'
      },
      foto1: {
        title: 'Foto',
        editable: false,
        type: 'custom',
        renderComponent: VisitorsGetmediaRenderComponent,
      },
    }
  };
  showConfirmModal: EventEmitter<any> = new EventEmitter(); // mostra la modal
  onModalResult = new Subject<boolean>(); // callback per la modal

  newVisitForm = JSON.stringify({
    name: 'Nuova visita',
    showTitle: false,
    excludeForEmit: ['closed'],
    actions: [],
    elements: [
      {
        name: 'orario',
        label: 'Ingresso/Uscita',

        ctrltype: CtrlTypesEnum.DATETIMERANGE,
        disabled: false,

        min: moment().format('YYYY-MM-DD HH:mm'),

        validations: [
          {type: 'testdatatimerangenostartdate', errMsg: 'Occorre inserire un valore per la \'Data di ingresso\''},
          {type: 'testdatatimerangenostarttime', errMsg: 'Occorre inserire un valore per l\' \'Orario di ingresso\''},
          {type: 'testdatatimerangenoenddate', errMsg: 'Occorre inserire un valore per la \'Data di uscita\''},
          {type: 'testdatatimerangenoendttime', errMsg: 'Occorre inserire un valore per l\' \'Orario di uscita\''},
          {type: 'testdatatimerangemin', errMsg: 'Occorre inserire una data uguale o superiore a quella corrente per la data di ingresso'},
          /*                    {type: 'testdatatimerangemax', errMsg: 'Occorre inserire una data minore o uguale al \'30/12/2018\' per la data di uscita'},*/
          {type: 'testdatatimerangenotvalid', errMsg: 'La data di ingresso deve essere minore o uguale alla data di uscita'}
        ],
        formState: 'opened',
        groupElChilds: [
          {
            label: 'Data Ingresso',
            value: momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD'),
          },
          {
            label: 'Ora Ingresso',
            value: momentTimezone.tz('Europe/Rome').format('HH:mm')
          },
          {
            label: 'Data Uscita',
            value: momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD')
          },
          {
            label: 'Ora uscita',
            value: '18:00'
          },
        ]
      },
      {
        name: 'note',
        label: 'Note aggiuntive',
        placeholder: 'Digita qui...',
        disabled: false,

        ctrltype: CtrlTypesEnum.TEXTAREA,
        valuetype: CtrlValueTypeEnum.STRING,

        validations: [],
        formState: 'opened',
        value: null
      }
    ]
  });
  visitFormChanged = false;
  invalidForm: boolean;
  newVisit: IVisitCreateRequest;

  private filesUploadedGuard = 0;
  newVisitorInEdit = true;

  interval;

  loading: boolean = false;
  showSolariResponseHelper: { msg: string };

  constructor(private visitService: VisitService,
              private visitorService: VisitorService,
              private mediaService: MediaService,
              private ngxNotificationsService: NgxNotificationsService,
              private vrWebSocket: VrWebSocketService,
              private statusDetailService: StatusDetailService,
  ) {
  }

  ngOnInit() {

    this.wizard.navService.currentPageChanged.subscribe((next) => {
      if (next.stepItemId === 'clr-wizard-step-0') {
        this.visitFormChanged = false;
      }

      if (this.visitFormChanged === true) {
        this.createOrUpdateVisit(this.newVisit);
        this.visitFormChanged = false;
      }
    });

    // Inizializzo la form
    const today = momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD');
    this.interval = setInterval(() => {     // TODO risolvere questo problema
      this.openChange.emit(this.open);
    }, 1000);
    if (this.visit) {
      if (this.visit.locked === true || !this.isUserInCall()) { // se non sto chiamando,  la edit di una visita non confermata
        this.newVisit = this.convertTimezone(this.visit);
        this.settings.actions = null;
        this.jumpTo(this.pageSummary);
      } else {
        this.initializeJSONForm(this.visit);
        this.onNewVisitValuesChange({
          values: {
            id: this.visit.id,
            note: this.visit.note,
            orario: {
              startDate: momentTimezone.tz(this.visit.startDate, 'Europe/Rome').format('YYYY-MM-DD'),
              startTime: momentTimezone.tz(this.visit.startDate, 'Europe/Rome').format('HH:mm'),
              endDate: momentTimezone.tz(this.visit.endDate, 'Europe/Rome').format('YYYY-MM-DD'),
              endTime: momentTimezone.tz(this.visit.endDate, 'Europe/Rome').format('HH:mm'),
            },
            callRequest: this.visit.callRequest
          }
        });
      }
    } else {
      this.onNewVisitValuesChange({
        values: {
          note: null,
          orario: {
            startDate: today,
            startTime: momentTimezone.tz('Europe/Rome').format('HH:mm'),
            endDate: today,
            endTime: '18:00'
          }
        }
      });
    }
  }

  ngOnDestroy(): void {
    clearInterval(this.interval);
  }

  initializeJSONForm(visit: IVisit) {
    this.newVisitForm = JSON.stringify({
      name: 'Nuova visita',
      showTitle: false,
      excludeForEmit: ['closed'],
      actions: [],
      elements: [
        {
          name: 'orario',
          label: 'Ingresso/Uscita',

          ctrltype: CtrlTypesEnum.DATETIMERANGE,
          disabled: false,

          min: moment().format('YYYY-MM-DD HH:mm'),

          validations: [
            {type: 'testdatatimerangenostartdate', errMsg: 'Occorre inserire un valore per la \'Data di ingresso\''},
            {type: 'testdatatimerangenostarttime', errMsg: 'Occorre inserire un valore per l\' \'Orario di ingresso\''},
            {type: 'testdatatimerangenoenddate', errMsg: 'Occorre inserire un valore per la \'Data di uscita\''},
            {type: 'testdatatimerangenoendttime', errMsg: 'Occorre inserire un valore per l\' \'Orario di uscita\''},
            {
              type: 'testdatatimerangemin',
              errMsg: 'Occorre inserire una data uguale o superiore a quella corrente per la data di ingresso'
            },
            /*                    {type: 'testdatatimerangemax', errMsg: 'Occorre inserire una data minore o uguale al \'30/12/2018\' per la data di uscita'},*/
            {type: 'testdatatimerangenotvalid', errMsg: 'La data di ingresso deve essere minore o uguale alla data di uscita'}
          ],
          formState: 'opened',
          groupElChilds: [
            {
              label: 'Data Ingresso',
              value: visit ? momentTimezone.tz(visit.startDate, 'Europe/Rome').format('YYYY-MM-DD') : momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD'),
            },
            {
              label: 'Ora Ingresso',
              value: visit ? momentTimezone.tz(visit.startDate, 'Europe/Rome').format('HH:mm') : momentTimezone.tz('Europe/Rome').format('HH:mm'),
            },
            {
              label: 'Data Uscita',
              value: visit ? momentTimezone.tz(visit.endDate, 'Europe/Rome').format('YYYY-MM-DD') : momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD'),
            },
            {
              label: 'Ora uscita',
              value: visit ? momentTimezone.tz(visit.endDate, 'Europe/Rome').format('HH:mm') : '18:00',
            },
          ]
        },
        {
          name: 'note',
          label: 'Note aggiuntive',
          placeholder: 'Digita qui...',
          disabled: false,

          ctrltype: CtrlTypesEnum.TEXTAREA,
          valuetype: CtrlValueTypeEnum.STRING,

          validations: [],
          formState: 'opened',
          value: visit.note
        }
      ]
    });
  }

  onNewVisitValuesChange(event) {
    const values = event.values;
    if (this.newVisit) {
      this.newVisit.startDate = momentTimezone.tz(values.orario.startDate + ' ' + values.orario.startTime, 'YYYY-MM-DD hh:mm', 'Europe/Rome').format();
      this.newVisit.endDate = momentTimezone.tz(values.orario.endDate + ' ' + values.orario.endTime, 'YYYY-MM-DD hh:mm', 'Europe/Rome').format();
      this.newVisit.note = values.note;
    } else {
      this.newVisit = {
        id: values.id,
        startDate: momentTimezone.tz(values.orario.startDate + ' ' + values.orario.startTime, 'YYYY-MM-DD hh:mm', 'Europe/Rome').format(),
        endDate: momentTimezone.tz(values.orario.endDate + ' ' + values.orario.endTime, 'YYYY-MM-DD hh:mm', 'Europe/Rome').format(),
        note: values.note,
        callRequest: {
          id: this.callId.toString()
        }
      };
    }
    if (event.returnAction === 'edit') {
      this.visitFormChanged = true;
      this.invalidForm = event.invalidFields.length > 0;
    }
  }

  public jumpTo(page: ClrWizardPage) {
    if (page) {
      this.wizard.navService.currentPage = page;
    } else {
      this.wizard.navService.setLastEnabledPageCurrent();
    }
    this.wizard.open();
  }

  public doCustomClick(buttonType: string): void {
    if ('custom-next' === buttonType) {
      this.createOrUpdateVisit(this.newVisit, true);
    }

    if ('custom-previous' === buttonType) {
      this.wizard.previous();
    }
    // step 2
    if ('custom-add' === buttonType) {
      this.newVisitorInEdit = true;
    }
    if ('custom-reset-new-user' === buttonType) {
      this.resetVisitorForm();
    }
    if ('custom-submit-new-user' === buttonType) {
      this.createOrUpdateVisitor(this.newVisit, this.checkInForm, []);
    }

    if ('custom-submit-cancel-user' === buttonType) {
      this.newVisitor = null;
      this.newVisitorInEdit = false;
    }
    // step 3
    if ('custom-confirm' === buttonType) {
      this.confirmVisit(this.newVisit);
    }
    if ('custom-delete-visit' === buttonType) {
      this.deleteVisit(this.newVisit);
    }
  }

  createOrUpdateVisit(newVisit, goNextStep?: boolean) {
    this.loading = true;
    if (!newVisit.id) {
      this.visitService.create(newVisit).subscribe(
        (response) => {
          this.newVisit = this.convertTimezone(response);
          if (goNextStep === true) {
            this.wizard.next();
          }
          this.loading = false;
        },
        error => {
          this.loading = false;
        }
      );
    } else {
      this.visitService.get(newVisit.id).subscribe(
        (response) => {
          newVisit.callRequest = response.callRequest;
          newVisit.visitors = response.visitors;
          this.visitService.update(newVisit).subscribe(
            (response1) => {
              this.newVisit = this.convertTimezone(response1);
              if (goNextStep === true) {
                this.wizard.next();
              }
              this.loading = false;
            },
            error => {
              this.loading = false;
            }
          );
        },
        error => {
          this.loading = false;
        }
      );
    }
  }

  createOrUpdateVisitor(newVisit, checkInForm: FormGroup, formDataArr) {
    if (checkInForm.valid && this.acquiredDocRetro && this.acquiredDocFront && this.acquiredUser) {
      const newVisitorCreateRequest: IVisitorCreateRequest = {
        name: checkInForm.value.firstName,
        surname: checkInForm.value.lastName,
        visit: newVisit,
        attachments: []
      };
      this.loading = true;
      this.visitorService.create(newVisitorCreateRequest).subscribe(
        (response) => {
          this.newVisitor = response as IVisitor;
          this.newVisit.visitors = this.newVisit.visitors ? this.newVisit.visitors.push(response as IVisitor) : [response as IVisitor];
          this.filesUploadedGuard = 0;
          this.uploadMedia(this.newVisitor, this.canvasUser);
          this.uploadMedia(this.newVisitor, this.canvasDocFront);
          this.uploadMedia(this.newVisitor, this.canvasDocRetro);
        },
        error => {
          this.loading = false;
        }
      );
    } else {
      this.ngxNotificationsService.error({body: 'Compilare tutti i campi.  inoltre necessario acquisire le foto dell \'utente e ed il fronte\\retro del documento'});
    }
  }

  uploadMedia(visitor: IVisitor, formData: FormData) {
    this.mediaService.upload(formData)
      .subscribe(
        (response: IMedia) => {
          const data: IVisitorAttachmentRequest = {
            id: null,
            attachmentMasterType: 'VISITOR',
            attachmentMasterId: visitor.id,
            mediaId: response.id
          };
          this.visitorService.attachmentCreate(data).subscribe(
            (response1) => {
              this.filesUploadedGuard = this.filesUploadedGuard + 1;

              if (this.filesUploadedGuard >= 3) {
                this.loading = false;
                this.resetVisitorForm();
                this.visitService.get(this.newVisit.id).subscribe(
                  (visit: IVisit) => {
                    this.newVisit = this.convertTimezone(visit);
                  }
                );
              } else {
                this.newVisitorInEdit = true;
              }
            },
            error => {
              this.loading = false;
            }
          );
        },
        error => {
          this.loading = false;
        }
      );
  }

  confirmVisit(newVisit) {
    this.loading = true;
    this.showSolariResponseHelper = null;

    if (!newVisit.locked) {
      const subscribeArr = [];
      newVisit.visitors.forEach(visitor => {
        if (!visitor.solariResponse || visitor.solariResponse === null) {
          subscribeArr.push(this.visitorService.sendToSolari(visitor));
        }
      });

      forkJoin(
        subscribeArr
      ).subscribe(
        (responseArr) => {
          responseArr.forEach((responseVisitor: IVisitor) => {
            newVisit.visitors.forEach((visitor: IVisitor) => {
              if (responseVisitor.id === visitor.id) {
                visitor.solariResponse = responseVisitor.solariResponse;
              }
            });
          });

          this.visitService.confirm(newVisit).subscribe(
            (response) => {
              this.newVisit = this.convertTimezone(response);
              this.ngxNotificationsService.success({body: 'Visita creata correttamente. La stampa dei ticket  in corso'});
              setTimeout(() => {
                this.vrWebSocket.sendTicketRequest(this.convertTimezone(response));
                this.wizard.close();
                this.loading = false;
              }, 1000);
            },
            error => {
              this.showSolariResponseHelper = {
                msg: 'Si  verificato un errore durante la conferma del check-in. Si prega di riprovare.'
              };
              this.loading = false;
            }
          );
        },
        error => {
          console.log('responseArr err', error);
          // qualche chiamata di solari non  andata a buon fine
          this.showSolariResponseHelper = {
            msg: 'Si  verificato un errore durante la creazione dei visitatori. Si prega di riprovare.'
          };
          this.loading = false;
        }
      );
    } else {
      this.vrWebSocket.sendTicketRequest(newVisit);
      this.ngxNotificationsService.success({body: 'Comando inviato correttamente'});
      this.loading = false;
    }
  }

  deleteVisit(newVisit: IVisitCreateRequest) {
    const visitToDelete: IVisit = newVisit as IVisit;
    const subscription = this.onModalResult.subscribe(success => {
      if (success === true) {
        this.loading = true;
        this.visitService.delete(visitToDelete).subscribe(
          () => {
            this.ngxNotificationsService.success({body: 'La visita  stata cancellata correttamente.'});
            if (!this.isUserInCall()) {
              this.wizard.close();
            } else {
              this.resetVisitForm();
              this.jumpTo(this.pageNewVisit);
            }
            this.loading = false;
          },
          error => {
            this.loading = false;
          }
        );
      }
      subscription.unsubscribe();
    });
    this.showConfirmModal.emit({type: ModalContent.DELETE_CONTENT, item: 'la visita'});
  }

  convertTimezone(visitResponse) {
    visitResponse.startDate = momentTimezone.tz(visitResponse.startDate, 'Europe/Rome').format();
    visitResponse.endDate = momentTimezone.tz(visitResponse.endDate, 'Europe/Rome').format();
    return visitResponse;
  }

  resetVisitForm() {
    const today = momentTimezone.tz('Europe/Rome').format('YYYY-MM-DD');
    this.newVisit = null;
    this.onNewVisitValuesChange({
      values: {
        note: null,
        orario: {
          startDate: today,
          startTime: momentTimezone.tz('Europe/Rome').format('HH:mm'),
          endDate: today,
          endTime: '18:00'
        }
      }
    });
  }

  resetVisitorForm() {
    this.newVisitor = null;
    this.checkInForm.value.firstName = null;
    this.checkInForm.value.lastName = null;
    this.newVisitorInEdit = false;
    this.canvasUser = null;
    this.canvasDocFront = null;
    this.canvasDocRetro = null;
    this.acquiredUser = null;
    this.acquiredDocFront = null;
    this.acquiredDocRetro = null;
  }

  toggleUserCheckin() {
    this.open = !this.open;
    this.openChange.emit(this.open);
  }

  deleteVisitor(event) {
    const visitorToDelete: IVisitor = event.data;
    const subscription = this.onModalResult.subscribe(success => {
      if (success === true) {
        this.loading = true;
        this.visitorService.delete(visitorToDelete).subscribe(
          () => {
            this.ngxNotificationsService.success({body: 'L\'utente  stato cancellato correttamente.'});
            this.newVisit.visitors = this.newVisit.visitors.filter(visitor => visitor.id !== visitorToDelete.id);
            this.loading = false;
          },
          error => {
            this.loading = false;
          }
        );
      }
      subscription.unsubscribe();
    });
    this.showConfirmModal.emit({type: ModalContent.DELETE_CONTENT, item: visitorToDelete.name + '' + visitorToDelete.surname});
  }

  /*****/

  captureUser() {
    return !this.videoReverseOrdering ? this.videoUser : this.videoScanner;
  }

  captureScanner() {
    return !this.videoReverseOrdering ? this.videoScanner : this.videoUser;
  }

  /*****/

  isUserInCall() {
    return this.statusDetailService.getStatus().code === CALLSTATUS.CALL;
  }
}
