import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent} from './components/home/home.component';
import {CheckWebcamComponent} from './components/check-webcam/check-webcam.component';
import { EchoComponent} from './components/echo/echo.component';
import { BankBranchesComponent } from './components/bank-branches/bank-branches.component';
import { SmartDesksComponent } from './components/smart-desks/smart-desks.component';
import { ChequeBooksComponent } from './components/cheque-books/cheque-books.component';
import { CardListComponent } from './components/card-list/card-list.component';
import { ChequeBooksDeliveryComponent } from './components/cheque-books-delivery/cheque-books-delivery.component';
import { ChequeBooksIngestionComponent } from './components/cheque-books-ingestion/cheque-books-ingestion.component';
import { ChequeBooksLoadingComponent } from './components/cheque-books-loading/cheque-books-loading.component';
import { ChequeBooksViewComponent } from './components/cheque-books-view/cheque-books-view.component';
import { CreditCardViewComponent } from './components/credit-card-view/credit-card-view.component';
import { CreditCardDeliveryComponent } from './components/credit-card-delivery/credit-card-delivery.component';
import { CreditCardLoadingComponent } from './components/credit-card-loading/credit-card-loading.component';
import { CreditCardIngestionComponent } from './components/credit-card-ingestion/credit-card-ingestion.component';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'home',
        component: HomeComponent
      },
/*      {
        path: 'home',
        component: VideoRoomComponent
      },*/
/*      {
        path: 'call',
        component: VideoCallComponent
      },*/
      {
        path: 'echo',
        component: EchoComponent
      },
      {
        path: 'check-webcam',
        component: CheckWebcamComponent
      },
      {
        path: 'bank-branches',
        component: BankBranchesComponent
      },
      {
        path: 'smart-desks',
        component: SmartDesksComponent
      },
        {
          path: 'smart-desks/:id',
          component: SmartDesksComponent
        },
          {
            path: 'smart-desks/:checkbook/:id',
            component: SmartDesksComponent
          },
          {
            path: 'smart-desks/:credit/:id',
            component: SmartDesksComponent
          },
        {
        path: 'cheque-books/:id',
        component: ChequeBooksComponent
      },
      {
        path: 'card-list/:id',
        component: CardListComponent
      },
      {
        path: 'cheque-books',
        component: ChequeBooksComponent
      },
        {
          path: 'cheque-books-view/:id',
          component: ChequeBooksViewComponent
        },

      {
        path: 'card-list',
        component: CardListComponent
      },
        {
          path: 'credit-card-view/:id',
          component: CreditCardViewComponent
        },
        {
          path: 'cheque-books-loading',
          component: ChequeBooksLoadingComponent
        },
          {
            path: 'cheque-books-loading/:id',
            component: ChequeBooksLoadingComponent
          },
        {
          path: 'cheque-books-delivery',
          component: ChequeBooksDeliveryComponent
        },
          {
            path: 'cheque-books-delivery/:id',
            component: ChequeBooksDeliveryComponent
          },
        {
          path: 'cheque-books-ingestion',
          component: ChequeBooksIngestionComponent
        },
          {
            path: 'cheque-books-ingestion/:id',
            component: ChequeBooksIngestionComponent
          },
        {
          path: 'credit-cards-delivery',
          component: CreditCardDeliveryComponent
        },
          {
            path: 'credit-cards-delivery/:id',
            component: CreditCardDeliveryComponent
          },
        {
          path: 'credit-cards-loading',
          component: CreditCardLoadingComponent
        },
          {
            path: 'credit-cards-loading/:id',
            component: CreditCardLoadingComponent
          },
        {
          path: 'credit-cards-ingestion',
          component: CreditCardIngestionComponent
        },
          {
            path: 'credit-cards-ingestion/:id',
            component: CreditCardIngestionComponent
          },
      {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
      },
      {
        path: '**',
        redirectTo: 'home',
        pathMatch: 'full'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StartVrRoutingModule { }
