import {Component, Input, OnInit} from '@angular/core';
import {ViewCell} from 'ng2-smart-table';
import {TranslateService} from '@ngx-translate/core';
import {IVisit} from '../../../../interfaces/iVisit';

@Component({
  selector: 'app-visit-icon-render-component',
  templateUrl: './visit-icon-render-component.html'
})
export class VisitIconRenderComponent implements ViewCell, OnInit {
  visit: IVisit;

  @Input() value: any;
  @Input() rowData: any;

  constructor(private translate: TranslateService) {
  }

  ngOnInit() {
    this.translate.get(
      'vr.common.notDefined'
    ).subscribe(
      result => {
        this.visit = this.rowData;
      });
  }

}
