import {Injectable} from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {IVisitCreateRequest} from '../../interfaces/iVisitCreateRequest';
import {IVisitSearchRequest} from '../../interfaces/iVisitSearchRequest';
import {IVisit} from '../../interfaces/iVisit';
import {IPageable} from 'ngx-uaa/lib/interfaces/iPageable';
import {HttpParams} from '@angular/common/http';
import {RequestOptions} from '@angular/http';

@Injectable({
  providedIn: 'root'
})
export class VisitService extends HttpCommonService {

  private basePath = '/api/visit/';

  search(pageable: IPageable, search: IVisitSearchRequest, skipErrorInterceptor?: boolean) {
    const options = this.getOptions(skipErrorInterceptor);
    options['params'] = pageable as HttpParams;

    return this.http.post<any>(this.getServerUrl(this.basePath) + 'search', search, options);
  }

  get(idVisit: string, skipErrorInterceptor?: boolean) {
    return this.http.get<IVisit>(this.getServerUrl(this.basePath) + idVisit, this.getOptions(skipErrorInterceptor));
  }

  create(data: IVisitCreateRequest, skipErrorInterceptor?: boolean) {
    return this.http.post<IVisit>(this.getServerUrl(this.basePath), data, this.getOptions(skipErrorInterceptor));
  }

  update(data: IVisit, skipErrorInterceptor?: boolean) {
    return this.http.put<IVisit>(this.getServerUrl(this.basePath), data, this.getOptions(skipErrorInterceptor));
  }

  delete(data: IVisit, skipErrorInterceptor?: boolean) {
    const options = this.getOptions(skipErrorInterceptor);
    options['body'] = data;
    return this.http.delete<any>(this.getServerUrl(this.basePath), options);
  }

  confirm(data: IVisit, skipErrorInterceptor?: boolean) {
    return this.http.post<IVisit>(this.getServerUrl(this.basePath) + 'confirm', data, this.getOptions(skipErrorInterceptor));
  }
}
