import {Component, Input, OnInit} from '@angular/core';
import {ViewCell} from 'ng2-smart-table';
import {TranslateService} from '@ngx-translate/core';
import {IVisit} from '../../../../interfaces/iVisit';

@Component({
  selector: 'app-visit-call-request-totem-render',
  template: '{{renderValue}}'
})
export class VisitCallRequestTotemRenderComponent implements ViewCell, OnInit {

  renderValue: string;

  @Input() value: any;
  @Input() rowData: IVisit;

  constructor(private translate: TranslateService) {
  }

  ngOnInit() {
    this.translate.get(
      'vr.common.notDefined'
    ).subscribe(
      result => {
        this.renderValue = this.rowData.callRequest.caller.caller;
      });
  }


}
