import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent } from 'ng2-smart-table/ng2-smart-table.component';
import { CBooksDelivery } from '../../interfaces/cBooksDelivery';
import { ChequeBookDeliveryService } from '../../services/http/cheque-book-delivery.service';
import { environment } from '../../../../../environments/environment';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-cheque-books-delivery',
  templateUrl: './cheque-books-delivery.component.html',
  styleUrls: ['./cheque-books-delivery.component.scss']
})
export class ChequeBooksDeliveryComponent implements OnInit {

  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  rootURL = environment.baseUrl;
  data: CBooksDelivery [];
  displaySearch = true;
  settings = {
    actions: {
      edit: false,
      add: false,
      delete: false,
      position: 'right',
        columnTitle: 'Action'
      },

    columns: {
        id: {
          title: 'Id',
          filter: false,
          editable: true,
          addable: false
        },
        status: {
          title: 'status',
          filter: false,
          editable: true,
          addable: false
        },
        chequeBookId: {
          title: 'chequeBookId',
          filter: false,
          editable: true,
          addable: false
        },
        smartDeskId: {
          title: 'smartDeskId',
          filter: false,
          editable: true,
          addable: false
        },
        bankclientId: {
          title: 'bankclientId',
          filter: false,
          editable: true,
          addable: false
        }
      }
  };

  id:any="";
  constructor(private http: HttpClient, private route: ActivatedRoute, private cbDeliveryService: ChequeBookDeliveryService){
    this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      console.log(this.id);
      if(this.id != null){this.loadDataByCBId(); this.displaySearch=false;}else{this.loadData();}
    });
  }

  onSearch(query: string = '') {
    console.log(query);
    if(query != ''){
      this.http.get<CBooksDelivery>(this.rootURL+'/search/transaction/chequebook/delivery/'+query).subscribe(
        res => {
          console.log(res);
          this.data = res['data'];
        },
        (err: HttpErrorResponse) => {
            if (err.error instanceof Error) {
              console.log("Client-side error occured.");
            } else {
              console.log("Server-side error occured.");
            }
          });
        //console.log(this.actionValue);
        }else{
          this.loadData();
      }
    }
 //OnChange load data
  ngOnChanges(){
   // this.loadData();
  }

  //Load All Data
  loadData(){
    this.cbDeliveryService.getAll().subscribe((data : CBooksDelivery[]) => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  ngOnInit(){
    //this.loadData();
  };

  //Load data by chequebookid
  loadDataByCBId(){
    this.cbDeliveryService.getCheckBookId(this.id).subscribe(data  => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

}

