import { HttpClient, HttpErrorResponse, HttpHeaders  } from '@angular/common/http';
import { Component, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { Ng2SmartTableComponent} from 'ng2-smart-table/ng2-smart-table.component';
import { BBranches } from '../../interfaces/bBraches';
import { BBranchesRequest } from '../../interfaces/bBrachesRequest';
import { BankBranchesService } from '../../services/http/bank-branches.service';
import { VisitIconRenderComponent } from '../dynamic-table/dynamic-table-render-components/visit-icon-render-component/visit-icon-render-component';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-bank-branches',
  templateUrl: './bank-branches.component.html',
  styleUrls: ['./bank-branches.component.scss']
})
export class BankBranchesComponent implements OnInit {

  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  rootURL = environment.baseUrl;
    errmsg:boolean = false;
    display_msg:any="";
    data: BBranches [];
    actionValue:boolean = true;
    settings = {

      delete :{
        confirmDelete: true
      },
      add:{
        confirmCreate:true
      },
      edit:{
        confirmSave:true
      },
      actions: {
        position: 'right',
        columnTitle: 'Action',
        edit: this.actionValue,
        add: this.actionValue,
        delete: this.actionValue,
      },

    columns: {
      id: {
          title: 'Id',
          filter: false,
          editable: false,
          addable: false
        },
        branchName: {
          filter: false,
          title: 'Branch',
          type: 'string',
        },
        externalId: {
          filter: false,
          title: 'External Id',
          type: 'number',
        },
        noOfSmartDesk: {
          filter: false,
          editable: false,
          addable: false,
          title: 'Smart desks',
          type: 'html',
          valuePrepareFunction: (cell: any, row: { id: any; noOfSmartDesk: any; }) => {
            return `<a href="/vr/smart-desks/${row.id}" class="smart_desk_path">${row.noOfSmartDesk}</a>`;
          }
        }
    }
  };

  refreshing: boolean;
  LocalDataSource = new LocalDataSource();
  constructor(private http: HttpClient,  private bankBranchesService : BankBranchesService){
    console.log(this.data);

  }

onSearch(query: string = '') {
  console.log(query);
  if(query != ''){
    this.http.get<BBranches>(this.rootURL+'/search/bankbranch/'+query).subscribe(
      res => {
        console.log(res);
        this.data = res['data'];
      },
      (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log("Client-side error occured.");
          } else {
            console.log("Server-side error occured.");
          }
        });
      //console.log(this.actionValue);
      }else{
        this.loadData();

    }

  }

 // Add record to table
  addRecord(event: { newData: { branchName: any; externalId: any; }; confirm: { resolve: (arg0: any) => void; }; }) {
    var  BBranchesRequest ={
                            "branchName" : event.newData.branchName,
                            "externalId" : event.newData.externalId,
                          };
                          console.log(event);
      console.log(BBranchesRequest);
      if(event.newData.branchName !="" &&  event.newData.externalId != "" && !isNaN(event.newData.externalId)) {
        this.bankBranchesService.create(BBranchesRequest).subscribe(
          res => {
            console.log(res);
            event.confirm.resolve(event.newData);
            this.bankBranchesService.getAll().subscribe((data : BBranches[]) => {
              this.data = data['data'];
              console.log(this.data);
            });
        },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log("Client-side error occured.");
          } else {
            console.log("Server-side error occured.");
          }
        });
      }else{
        //alert("All fields are required !");
        //console.table(event);
        this.errmsg = true;
        this.display_msg="All fields are required & external_Id must be Number !";
          setTimeout (() => {
            this.errmsg = false;
        }, 2500);
      }

  }


  // update record in table
  updateRecord(event: { newData: { id: any; branchName: any; externalId: any; noOfSmartDesk: null}; confirm: { resolve: (arg0: any) => void; }; }) {
    var data = {
                "id" : event.newData.id,
                "branchName" : event.newData.branchName,
                "externalId" : event.newData.externalId,
                "noOfSmartDesk" : null,
                };
                //console.log(data);
    if(event.newData.branchName !="" &&  event.newData.externalId != "" && !isNaN(event.newData.externalId))
    {
      this.bankBranchesService.update(data).subscribe(
        res => {
         // console.log(res);
          event.confirm.resolve(event.newData);
          this.loadData();
      },
      (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          console.log("Client-side error occured.");
        } else {
          console.log("Server-side error occured.");
        }
      });
    }else{
        // alert("All Fields are required !");
        // console.table(event);
        this.errmsg = true;
        this.display_msg="All fields are required & external_Id must be Number !";
          setTimeout (() => {
            this.errmsg = false;
        }, 2500);
    }

  }

  // Delete Records
  deleteRecord(event: any){
    console.log(event.data.id);
    if (window.confirm('Are you sure you want to delete?')) {
      event.confirm.resolve();
        this.bankBranchesService.delete(event.data.id).subscribe(
        res => {
         // console.log(res);
          event.confirm.resolve(event.newData);
      },
      (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          console.log("Client-side error occured.");
        } else {
          console.log("Server-side error occured.");
        }
      });
    } else {
      event.confirm.reject();
    }

  }

  ngOnChanges(){
    this.loadData();
  }

  loadData(){
    this.bankBranchesService.getAll().subscribe((data : BBranches[]) => {
      this.data = data['data'];
      console.log(this.data);
    });
  }

  onCustomAction(event){
    console.log(event);
  }


  ngOnInit(){
    this.loadData();
  };


}
