import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {StatusDetailService} from '../../services/status-detail.service';
import IJanusPluginHandle = JanusStatic.IJanusPluginHandle;
import {VIDEOQUALITY} from '../../enums/videoquality.enum';
import {VIDEOBITRATE} from '../../enums/videobitrate.enum';

@Component({
  selector: 'app-call-controls',
  templateUrl: './call-controls.component.html',
  styleUrls: ['./call-controls.component.scss']
})
export class CallControlsComponent implements OnInit, OnDestroy {

  @Input() janusPluginHandle: IJanusPluginHandle;

  @Input() doHangup: (janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) => void;

  @Input() hideQualityCtrl?: boolean;

  @Input() isVideoRoomCall?: boolean;

  @Input() switchCam?: () => void;

  qualityRes = [
    {title: 'Bassa', code: VIDEOQUALITY.LOW},
    {title: 'Bassa (16:9)', code: VIDEOQUALITY.LOW_WIDE},
    {title: 'Normale', code: VIDEOQUALITY.STANDARD},
    {title: 'Normale  (16:9)', code: VIDEOQUALITY.STANDARD_WIDE},
    {title: 'Alta', code: VIDEOQUALITY.HIGH},
    {title: 'Alta (16:9)', code: VIDEOQUALITY.HIGH_WIDE}
  ];

  bitrateList = [
    {title: 'Nessun limite', code: VIDEOBITRATE.NOLIMIT},
    {title: '128 Kbit/sec', code: VIDEOBITRATE['128kbit']},
    {title: '256 Kbit/sec', code: VIDEOBITRATE['256kbit']},
    {title: '512 Kbit/sec', code: VIDEOBITRATE['512kbit']},
    {title: '1 Mbit/sec', code: VIDEOBITRATE['1mbit']},
    {title: '1.5 Mbit/sec', code: VIDEOBITRATE['1.5mbit']},
    {title: '2 Mbit/sec', code: VIDEOBITRATE['2mbit']},
  ];

  private bitrateSelected = this.bitrateList[this.bitrateList.length - 1];

  audioEnabled = true;

  videoEnabled = true;

  fullscreenEnabled = false;

  private homeContainer: Element;

  constructor(public statusDetailService: StatusDetailService) {

  }

  ngOnInit() {
    this.homeContainer = document.querySelector('#video-container');
  }

  doMyHangup(janusPluginHandle, statusDetailService) {
    this.toggleFullscreen(false);
    this.doHangup(janusPluginHandle, statusDetailService);
  }

  changeQuality(qualityCode: string) {
    this.janusPluginHandle.createOffer(
      {
        media: {   // By default, it's sendrecv for audio and video...
          data: false,
          video: qualityCode,
        },
        success: (jsep) => {
          this.janusPluginHandle.send({message: {audio: true, video: true}, jsep});
        },
        error: (error) => {
        }
      });

  }

  changeBitrate(bitrate: any) {
    this.bitrateSelected = bitrate;

    const body = {request: 'set', bitrate: parseInt(bitrate.code, 0) * 1000};
    this.janusPluginHandle.send({message: body});
  }

  toggleVideo(value: boolean) {
    this.videoEnabled = value;
    const requestType = this.isVideoRoomCall ? 'configure' : 'set';
    const body = {request: requestType, video: value};
    this.janusPluginHandle.send({message: body});
  }

  toggleAudio(value: boolean) {
    this.audioEnabled = value;
    const requestType = this.isVideoRoomCall ? 'configure' : 'set';
    const body = {request: requestType, audio: value};
    this.janusPluginHandle.send({message: body});
  }

  toggleFullscreen(value: boolean) {
    this.fullscreenEnabled = value;

    if (value === true) {
      this.homeContainer.classList.add('fullscreen');
    } else {
      this.homeContainer.classList.remove('fullscreen');
    }
  }

  ngOnDestroy(): void {
  }
}
