import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {CBooksList} from '../../interfaces/cBooksList';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CBooksView } from '../../interfaces/cBooksView';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ChequeBookListService {

  data : CBooksList[];
  bdata : CBooksView[];
  rootURL = environment.baseUrl;
  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/chequebooks`);
  }

  get(id: number) {
    return this.http.get<CBooksView>(`${this.rootURL}/find/chequebook/byid/` + id);
  }

  getBySmartID(id: number) {
    return this.http.get<CBooksView>(`${this.rootURL}/find/chequebook/bysmartdeskid/` + id);
  }

  // public create(bdata: BBranchesRequest) {
  //   return this.http.post<BBranchesRequest>(`${this.rootURL}/save/bankbranch`, bdata);
  // }

  update(bdata: CBooksView) {
    return this.http.post<CBooksView>(`${this.rootURL}/ingestion/chequebooks`, bdata);
  }

  delete(id: CBooksList) {
    return this.http.delete<CBooksList>(`${this.rootURL}/delete/bankbranch/${id}`);
  }

  search(any: string){
    this.http.get<CBooksList>(`${this.rootURL}/search/bankbranch/${any}`);
  }


}
