import {Component, Input, OnInit} from '@angular/core';
import {ViewCell} from 'ng2-smart-table';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-visit-status-render-component',
  templateUrl: './visit-status-render-component.html'
})
export class VisitStatusRenderComponent implements ViewCell, OnInit {
  locked: boolean;

  @Input() value: any;
  @Input() rowData: any;

  constructor(private translate: TranslateService) {
  }

  ngOnInit() {
    this.translate.get(
      'vr.common.notDefined'
    ).subscribe(
      result => {
        this.locked = this.rowData.locked;
      });
  }

}
