import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent } from 'ng2-smart-table/ng2-smart-table.component';
import { CBooksList} from '../../interfaces/CBooksList';
import { ChequeBookListService } from '../../services/http/cheque-book-list.service';
import { environment } from '../../../../../environments/environment';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-cheque-books',
  templateUrl: './cheque-books.component.html',
  styleUrls: ['./cheque-books.component.scss']
})
export class ChequeBooksComponent implements OnInit {

  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;
  rootURL = environment.baseUrl;
  data: CBooksList [];
  settings = {
    actions: {
      edit: false,
      confirmSave:false,
      add: false,
      delete: false,
      position: 'right',
        columnTitle: 'Action'
      },

    columns: {
        id: {
          title: 'Id',
          filter: false,
          editable: true,
          addable: false
        },
        cabAssegno: {
          title: 'cabAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        filAssegno: {
          title: 'filAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        nrPrimoAssegno: {
          title: 'nrPrimoAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        nrUltimoAssegno: {
          title: 'nrUltimoAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        nrAssegni: {
          title: 'nrAssegni',
          filter: false,
          editable: true,
          addable: false
        },
        cinPrimoAssegno: {
          title: 'cinPrimoAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        codiceUnivoco: {
          title: 'codiceUnivoco',
          filter: false,
          editable: true,
          addable: false
        },
        noOfSmartDesk: {
          title: 'Smart Desks',
          filter: false,
          editable: false,
          addable: false,
          type: 'html',
          valuePrepareFunction: (cell, row) => {
            return `<a href="/vr/smart-desks/checkbook/${row.id}" class="smart_desk_path">${row.noOfSmartDesk}</a>`;
          }
        },
        noOfIngestionTransaction: {
          title: 'Ingestion Transaction',
          filter: false,
          editable: false,
          addable: false,
          type: 'html',
            valuePrepareFunction: (cell, row) => {
              return `<a href="/vr/cheque-books-ingestion/${row.id}" class="smart_desk_path">${row.noOfIngestionTransaction}</a>`;
            }
        },
        noOfLodingTransaction: {
          title: 'Loading Transaction',
          filter: false,
          editable: false,
          addable: false,
          type: 'html',
          valuePrepareFunction: (cell, row) => {
            return `<a href="/vr/cheque-books-loading/${row.id}" class="smart_desk_path">${row.noOfLodingTransaction}</a>`;
          }
        },
        noOfDeliveryTransaction: {
            title: 'Delivery Transaction',
            filter: false,
            editable: false,
            addable: false,
            type: 'html',
            valuePrepareFunction: (cell, row) => {
              return `<a href="/vr/cheque-books-delivery/${row.id}" class="smart_desk_path">${row.noOfDeliveryTransaction}</a>`;
            }
        },
        Action: {
          filter: false,
          editable: false,
          addable: false,
          title: 'Action',
          type: 'html',
          valuePrepareFunction: (cell: any, row: { id: any; noOfSmartDesk: any; }) => {
            return `<a href="/vr/cheque-books-view/${row.id}" class="smart_desk_path">View</a>`;
          }
      },
    }
  };

  previousUrl: string;
  id:any="";
  constructor(private http: HttpClient, private route: ActivatedRoute,  private CBookListService: ChequeBookListService){
    this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      //alert( this.checkbook+" "+this.id);
      // console.log(this.id);
      if(this.id != null ){this.loadDataBySmartID();}else{this.loadData();}
    });
  }

  ngOnChanges(){
    if(this.id != null ){this.loadDataBySmartID();}else{this.loadData();}
  }

  // load all data from chequebooks
  loadData(){
    this.CBookListService.getAll().subscribe((data : CBooksList[]) => {
      this.data = data['data'];
      //console.log(this.data);
    });
  }

   //load data from card if smartdesk id is present
   loadDataBySmartID(){
    this.CBookListService.getBySmartID(this.id).subscribe(data  => {
      this.data = data['data'];
     // console.log(this.data);
    });
  }
  // serach funtion
  onSearch(query: string = '') {
    //console.log(query);
    if(query != ''){
      this.http.get<CBooksList>(this.rootURL+'/search/chequebooks/'+query).subscribe(
        res => {
          //console.log(res);
          this.data = res['data'];
        },
        (err: HttpErrorResponse) => {
            if (err.error instanceof Error) {
              console.log("Client-side error occured.");
            } else {
              console.log("Server-side error occured.");
            }
          });
        //console.log(this.actionValue);
        }else{
          this.loadData();
        }
    }

  ngOnInit(){
    if(this.id != null ){this.loadDataBySmartID();}else{this.loadData();}
  };


}
