import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-networking-modal',
  templateUrl: './networking-modal.component.html',
  styleUrls: ['./networking-modal.component.scss']
})
export class NetworkingModalComponent implements OnInit {

  @Input() opened: boolean;
  @Output() openedChange = new EventEmitter();

  navigator = navigator as any;

  constructor() {
  }

  ngOnInit() {
    setInterval(() => {
      this.openedChange.emit(this.opened);
    }, 1000); // TODO workaround
  }

}
