import {Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild} from '@angular/core';
import * as Janus from 'janus-typescript-client';
import {BehaviorSubject} from 'rxjs';
import {CALLSTATUS} from '../../enums/callstatus.enum';
import {StatusDetailService} from '../../services/status-detail.service';
import {IStatusDetail} from '../../interfaces/iStatusDetail';
import {LogService} from '../../services/log.service';
import {JanusService} from '../../services/janus.service';
import {fadeTrigger} from '../../../../animations/fade.animation';
import {PUBLISHER} from '../../enums/publisher.enum';
import {JANUSPLUGIN} from '../../enums/janusplugin';
import {VIDEOQUALITY} from '../../enums/videoquality.enum';
import {HttpProfileService} from 'ngx-uaa';
import {VrWebSocketService} from '../../services/socket/vr-web-socket.service';
import {WEBSOCKETCALLSTATUS} from '../../enums/sockets/websocketcallstatus.enum';
import IJanusPluginOptions = JanusStatic.IJanusPluginOptions;
import IJanusPluginHandle = JanusStatic.IJanusPluginHandle;


@Component({
  selector: 'app-video-room',
  templateUrl: './video-room.component.html',
  styleUrls: ['./video-room.component.scss'],
  animations: [fadeTrigger]
})
export class VideoRoomComponent implements OnInit, OnDestroy {

  // two way data binding
  @Input() showVideoCall: boolean;
  @Output() showVideoCallChange = new EventEmitter();

  @ViewChild('videoElement')
  private video: HTMLVideoElement;

  @ViewChild('remoteVideoElement')
  private remoteVideo: HTMLVideoElement;

  @ViewChild('remoteVideoElement2')
  private remoteVideo2: HTMLVideoElement;

  isCheckinModalOpen; // mostra nasconde il pip
  isPipVisible = true; // mostra nasconde il pip

  // tengo il riferimento degli stream, per switchare le finestre
  private streamRef = {
    remoteVideoStream: null,
    remoteVideoStream2: null,
    reverseOrder: false
  };

  private callID: number;

  private current;

  openUserCheckin = false;

  //private readonls.vrWebSocket.getLastCallRequest.id: number = 1002;      // TODO get from service

  // Monitoro lo stato della chiamata, il servizio mi restituisce il json per il template
  public CALLSTATUS = CALLSTATUS;
  statusDetail: IStatusDetail;
  private statusDetailBS: BehaviorSubject<IStatusDetail>;
  private statusDetailBSSubscription;

  // Servizio di log
  private logsBS: BehaviorSubject<any>;
  private logs: any [];

  // Oggetti Janus
  public janusPluginHandle: IJanusPluginHandle;

  // Configurazioni Janus
  private janusVideoRoomPluginOptions: IJanusPluginOptions = {
    plugin: JANUSPLUGIN.VIDEOROOM,
    success: (janusPluginHandle: IJanusPluginHandle) => { // Plugin attached! 'janusPluginHandle' is our handle
      this.logService.appendLog('INFO', 'janusPluginHandle', janusPluginHandle);

      this.janusPluginHandle = janusPluginHandle;
      this.janusService.setJanusPluginHandle(JANUSPLUGIN.VIDEOROOM, this.janusPluginHandle);

      this.callID = this.vrWebSocket.getLastCallRequestID();
      console.log('ID ROOM', this.callID);

      this.createRoom(this.callID, this.janusPluginHandle, this.statusDetailService); // Synch
      //this.statusDetailService.setStatus(CALLSTATUS.RINGING, this.getCurrentName(this.current));

    },
    error: (cause) => { // Couldn't attach to the plugin
      this.logService.appendLog('ERROR', 'janusPluginHandle', cause);
    },
    consentDialog: (on) => {
      // e.g., Darken the screen if on=true (getUserMedia incoming), restore it otherwise
    },
    onmessage: (msg: any, jsep) => {       // We got a message/event (msg) from the plugin. If jsep is not null, this involves a WebRTC negotiation
      if (jsep !== undefined && jsep !== null) {
        this.janusPluginHandle.handleRemoteJsep({jsep});
      }
      if (msg.videoroom) {
        this.logService.appendLog('INFO', 'message', msg);
        switch (msg.videoroom) {
          case 'joined': {
            this.statusDetailService.setStatus(CALLSTATUS.CALL, this.getCurrentName(this.current));

            // Publisher/manager created, negotiate WebRTC and attach to existing feeds, if any
            //this.getRoomParticipants(this.vrWebSocket.getLastCallRequest.id, this.janusPluginHandle, this.statusDetailService);
            this.publishFeed(this.vrWebSocket.getLastCallRequestID(), this.janusPluginHandle, this.statusDetailService);


            const body = {request: 'listparticipants', room: this.vrWebSocket.getLastCallRequestID()}; // synchronous
            this.janusPluginHandle.send({
              message: body, success: (response) => {
                switch (response.error_code) {
                  case 427: {
                    this.logService.appendLog('WARNING', 'create room request sent', response.error);
                    break;
                  }
                }
                msg.publishers = response;
                // Any new feed to attach to?
                if (msg.publishers.participants !== undefined && msg.publishers.participants !== null) {
                  this.subscribeAllParticipants(msg.publishers.participants, true);
                }
              }
            });  // Synchronous


            break;
          }
          case 'destroyed': {
            // The room has been destroyed
            break;
          }
          case 'event': {
            if (msg.configured && msg.configured === 'ok') {
              // ok, I'm publishing
              this.statusDetailService.setStatus(CALLSTATUS.CALL);
            }
            if (msg.publishers && msg.publishers.length > 0) {
              // new publisher joined and transmitting
              this.subscribeAllParticipants(msg.publishers);
            }
            if (msg.leaving) {
              //if (msg.leaving === PUBLISHER.SCANNERCAMERA || msg.leaving === PUBLISHER.REMOTECAMERA) {
              this.vrWebSocket.sendCallStatus(WEBSOCKETCALLSTATUS.HANGUP);
              //}
            }
            if (msg.error) {
              // this.statusDetailService.setStatus(CALLSTATUS.ERROR);
              switch (msg.error_code) {
                case 425: { // "Already in as a publisher on this handle"
                  this.publishFeed(parseInt(this.vrWebSocket.getLastCallRequest().id), this.janusPluginHandle, this.statusDetailService); // Asynch
                  break;
                }
                case 427: { // Room already exist
                  this.joinRoom(this.vrWebSocket.getLastCallRequestID(), this.janusPluginHandle, this.statusDetailService); // Asynch
                  break;
                }
              }
            }
            // Any new feed to attach to?
            break;
          }
        }
      }

    },
    onlocalstream: (stream) => {       // We have a local stream (getUserMedia worked!) to display
      if (this.video) {
        Janus.attachMediaStream(this.video, stream);
        this.video.muted = true;
        this.logService.appendLog('INFO', 'on local stream :', stream);
      } else {
        this.logService.appendLog('ERROR', 'on local stream : DOM video element not found');
      }
    },
    onremotestream: (stream) => {       // We have a remote stream (working PeerConnection!) to display
      if (this.remoteVideo) {
        Janus.attachMediaStream(this.remoteVideo, stream);
        this.logService.appendLog('INFO', 'on remote stream :', stream);
      } else {
        this.logService.appendLog('ERROR', 'on remote stream : DOM video element not found');
      }
    },
    oncleanup: () => {      // PeerConnection with the plugin closed, clean the UI. The plugin handle is still valid so we can create a new one
      this.logService.appendLog('INFO', 'on cleanup');
    },
    detached: () => {       // Connection with the plugin closed, get rid of its features. The plugin handle is not valid anymore
      this.logService.appendLog('INFO', 'detached');
    }
  };

  constructor(private janusService: JanusService,
              public statusDetailService: StatusDetailService,
              private logService: LogService,
              private httpProfileService: HttpProfileService,
              private vrWebSocket: VrWebSocketService) {
    this.logsBS = logService.getLogBS();

    this.statusDetailBS = statusDetailService.getStatusDetailBS();
    this.statusDetailBSSubscription = this.statusDetailBS.subscribe((next: IStatusDetail) => {
      if (next) {
        switch (next.code) {
          case CALLSTATUS.RINGING: {
            if (this.streamRef.reverseOrder === true) {
              this.switchCam()();
            }
            this.startJanusPlugin();
            this.toggleVideoCall(true);
            break;
          }
          case CALLSTATUS.FREE: {
            this.toggleVideoCall(false);
            break;
          }
          case CALLSTATUS.HANGUP: {
            this.unpublishFeed();
            setTimeout(() => {
              this.leaveRoom();
            }, 2500);
            break;
          }

        }
      }
      this.statusDetail = next;
    });
  }


  ngOnInit() {
    this.statusDetailService.setStatus(CALLSTATUS.NOT_REGISTRED);
    this.httpProfileService.current().subscribe(
      (response) => {
        this.current = response;
        this.vrWebSocket.initializeWebSocketConnection(this.current.identity.identity);

        if (this.areDOMElementsInitialized()) {
          this.statusDetailService.setStatus(CALLSTATUS.FREE, this.getCurrentName(this.current));
        } else {
          this.statusDetailService.setStatus(CALLSTATUS.ERROR);
          this.logService.appendLog('ERROR', 'document.querySelector');
        }
      }
    );
  }

  toggleVideoCall(showIt: boolean) {
    this.showVideoCall = showIt;
    this.showVideoCallChange.emit(this.showVideoCall);
  }

  startJanusPlugin() {
    this.janusPluginHandle = undefined;
    this.janusService.detachJanusPlugin(JANUSPLUGIN.VIDEOROOM);
    this.janusService.createJanusPluginHandle(JANUSPLUGIN.VIDEOROOM,
      this.janusVideoRoomPluginOptions,
      () => {
        //   this.statusDetailService.setStatus(CALLSTATUS.NOT_REGISTRED);
      }, (err) => {
        this.logService.appendLog('ERROR', 'on janus plugin creation', err);
        this.statusDetailService.setStatus(CALLSTATUS.ERROR);
      }
    );
  }

  restartJanusPlugin() {
    this.joinRoom(parseInt(this.vrWebSocket.getLastCallRequest().id), this.janusPluginHandle, this.statusDetailService); // Asynch
  }

  areDOMElementsInitialized() {
    try {
      const video: ElementRef = this.video as any;
      this.video = video.nativeElement as HTMLVideoElement;

      const remoteVideo: ElementRef = this.remoteVideo as any;
      this.remoteVideo = remoteVideo.nativeElement as HTMLVideoElement;

      const remoteVideo2: ElementRef = this.remoteVideo2 as any;
      this.remoteVideo2 = remoteVideo2.nativeElement as HTMLVideoElement;

      return (this.video && this.remoteVideo && this.remoteVideo2);
    } catch (e) {
      return false;
    }
  }

  createRoom(idRoom: number, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    //statusDetailService.setStatus(CALLSTATUS.SESSION_PENDING); // TODO:
    const body = {request: 'create', room: idRoom};
    janusPluginHandle.send({
      message: body, success: (response) => {
        switch (response.error_code) {
          case 427: {
            this.logService.appendLog('WARNING', 'create room request sent', response.error);
            break;
          }
        }
      }
    });  // Synchronous
  }

  joinRoom(idRoom: number, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    statusDetailService.setStatus(CALLSTATUS.SESSION_PENDING); // TODO:
    const body = {request: 'join', room: idRoom, ptype: 'publisher', id: PUBLISHER.REMOTECAMERA};
    janusPluginHandle.send({message: body});  // Asynchronous
  }

  leaveRoom() {
    const body = {request: 'leave'};
    this.janusPluginHandle.send({message: body});
  }

  publishFeed(idRoom: number, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    statusDetailService.setStatus(CALLSTATUS.CALL);
    janusPluginHandle.createOffer(
      {
        media: {video: VIDEOQUALITY.STANDARD_WIDE, audioRecv: true, videoRecv: true, audioSend: true, videoSend: true, data: true},
        success: (jsep) => {
          const body = {request: 'publish', room: idRoom};
          janusPluginHandle.send({message: body, jsep});
        },
        error: (error) => {
        }
      });
  }

  unpublishFeed() {
    const body = {request: 'unpublish'};
    this.janusPluginHandle.send({message: body});
  }

  doHangup() {
    return () => {
      this.vrWebSocket.sendCallStatus(WEBSOCKETCALLSTATUS.HANGUP);
    };
  }

  doResponse() {
    this.joinRoom(this.vrWebSocket.getLastCallRequestID(), this.janusPluginHandle, this.statusDetailService);
    this.vrWebSocket.sendCallStatus(WEBSOCKETCALLSTATUS.ANSWERED);
  }

  doReject() {
    this.vrWebSocket.sendCallStatus(WEBSOCKETCALLSTATUS.HANGUP);
  }

  getRoomParticipants(idRoom: number, janusPluginHandle: IJanusPluginHandle, statusDetailService?: StatusDetailService) {
    const body = {request: 'listparticipants', room: idRoom};
    janusPluginHandle.send({
      message: body, success: (response) => {
        switch (response.error_code) {
          case 427: {
            this.logService.appendLog('WARNING', 'create room request sent', response.error);
            break;
          }
        }
      }
    });  // Synchronous
  }

  subscribeAllParticipants(participantList: any[], alwaysPublisherObj?: boolean) {
    alwaysPublisherObj = alwaysPublisherObj ? alwaysPublisherObj : true;
    participantList.forEach((elem) => {
      if (elem.id !== PUBLISHER.REMOTECAMERA && (elem.publisher === true || alwaysPublisherObj)) {
        const idRoom = parseInt(this.vrWebSocket.getLastCallRequest().id);
        this.subscribeRemoteFeed(elem.id, idRoom, null, null, null);
        /*                      const id = list.id;
                              const display = list.display;
                              const audio = list.audio_codec;
                              const video = list.video_codec;
                              this.subscribeRemoteFeed(id, display, audio, video);*/
      }
    });
  }

  subscribeRemoteFeed(id, idRoom, display, audio, video) {
    // A new feed has been published, create a new plugin handle and attach to it as a subscriber
    let remoteFeed = null;
    this.janusService.getJanus(JANUSPLUGIN.VIDEOROOM).attach(
      {
        plugin: JANUSPLUGIN.VIDEOROOM,

        success: (pluginHandle) => {
          remoteFeed = pluginHandle;
          remoteFeed.simulcastStarted = false;
          // Janus.log('Plugin attached! (' + remoteFeed.getPlugin() + ', id=' + remoteFeed.getId() + ')');
          // Janus.log('  -- This is a subscriber');
          // We wait for the plugin to send us an offer
          const listen = {request: 'join', room: idRoom, ptype: 'subscriber', feed: id}; //, private_id: 1
          // In case you don't want to receive audio, video or data, even if the
          // publisher is sending them, set the 'offer_audio', 'offer_video' or
          // 'offer_data' properties to false (they're true by default), e.g.:
          // 		listen["offer_video"] = false;
          // For example, if the publisher is VP8 and this is Safari, let's avoid video
          /*          if (video !== 'h264' && Janus.webRTCAdapter.browserDetails.browser === 'safari') {
                      if (video) {
                        video = video.toUpperCase();
                      }
                      toastr.warning('Publisher is using ' + video + ', but Safari doesn\'t support it: disabling video');
                      listen.offer_video = false;
                    }*/
          remoteFeed.send({message: listen});
        },
        error(error) {
          /*          Janus.error('  -- Error attaching plugin...', error);
                    bootbox.alert('Error attaching plugin... ' + error);*/
        },
        onmessage(msg, jsep) {
          /*          Janus.debug(' ::: Got a message (subscriber) :::');
                    Janus.debug(msg);*/
          let event = msg.videoroom;
          // Janus.debug('Event: ' + event);
          if (msg.error !== undefined && msg.error !== null) {
            /* bootbox.alert(msg.error);*/
          } else if (event !== undefined && event !== null) {
            if (event === 'attached') {
              // Subscriber created and attached
              for (let i = 1; i < 6; i++) {
                /*                          if (feeds[i] === undefined || feeds[i] === null) {
                                            feeds[i] = remoteFeed;
                                            remoteFeed.rfindex = i;
                                            break;
                                          }*/
              }
              remoteFeed.rfid = msg.id;
              remoteFeed.rfdisplay = msg.display;
//                        Janus.log('Successfully attached to feed ' + remoteFeed.rfid + ' (' + remoteFeed.rfdisplay + ') in room ' + msg.room);
              //                      $('#remote' + remoteFeed.rfindex).removeClass('hide').html(remoteFeed.rfdisplay).show();
            } else if (event === 'event') {
              // Check if we got an event on a simulcast-related event from this publisher
              let substream = msg.substream;
              let temporal = msg.temporal;
              if ((substream !== null && substream !== undefined) || (temporal !== null && temporal !== undefined)) {
                if (!remoteFeed.simulcastStarted) {
                  remoteFeed.simulcastStarted = true;
                  // Add some new buttons
                  //               addSimulcastButtons(remoteFeed.rfindex, temporal !== null && temporal !== undefined);
                }
                // We just received notice that there's been a switch, update the buttons
                //                    updateSimulcastButtons(remoteFeed.rfindex, substream, temporal);
              }
            } else {
              // What has just happened?
            }
          }
          if (jsep !== undefined && jsep !== null) {
            //              Janus.debug('Handling SDP as well...');
            //               Janus.debug(jsep);
            // Answer and attach
            remoteFeed.createAnswer(
              {
                jsep,
                // Add data:true here if you want to subscribe to datachannels as well
                // (obviously only works if the publisher offered them in the first place)
                media: {video: VIDEOQUALITY.STANDARD_WIDE, audioSend: false, videoSend: false},	// We want recvonly audio/video
                success(jsep) {
                  //this.janus.debug('Got SDP!')
                  //                 Janus.debug('Got SDP!');
                  //                 Janus.debug(jsep);
                  let body = {request: 'start', room: idRoom};
                  remoteFeed.send({message: body, jsep});
                },
                error(error) {
                  //              Janus.error('WebRTC error:', error);
                  //            bootbox.alert('WebRTC error... ' + JSON.stringify(error));
                }
              });
          }
        },
        /*        webrtcState(on) {
                  //Janus.log('Janus says this WebRTC PeerConnection (feed #' + remoteFeed.rfindex + ') is ' + (on ? 'up' : 'down') + ' now');
                },
                onlocalstream(stream) {
                  // The subscriber stream is recvonly, we don't expect anything here
                },
                onremotestream(stream) {
                  //Janus.debug('Remote feed #' + remoteFeed.rfindex);
                  let addButtons = false;
                  if ($('#remotevideo' + remoteFeed.rfindex).length === 0) {
                    addButtons = true;
                    // No remote video yet
                    $('#videoremote' + remoteFeed.rfindex).append('<video class="rounded centered" id="waitingvideo' + remoteFeed.rfindex + '" width=320 height=240 />');
                    $('#videoremote' + remoteFeed.rfindex).append('<video class="rounded centered relative hide" id="remotevideo' + remoteFeed.rfindex + '" width="100%" height="100%" autoplay/>');
                    $('#videoremote' + remoteFeed.rfindex).append(
                      '<span class="label label-primary hide" id="curres' + remoteFeed.rfindex + '" style="position: absolute; bottom: 0px; left: 0px; margin: 15px;"></span>' +
                      '<span class="label label-info hide" id="curbitrate' + remoteFeed.rfindex + '" style="position: absolute; bottom: 0px; right: 0px; margin: 15px;"></span>');
                    // Show the video, hide the spinner and show the resolution when we get a playing event
                    $('#remotevideo' + remoteFeed.rfindex).bind('playing', function() {
                      if (remoteFeed.spinner !== undefined && remoteFeed.spinner !== null) {
                        remoteFeed.spinner.stop();
                      }
                      remoteFeed.spinner = null;
                      $('#waitingvideo' + remoteFeed.rfindex).remove();
                      if (this.videoWidth) {
                        $('#remotevideo' + remoteFeed.rfindex).removeClass('hide').show();
                      }
                      let width = this.videoWidth;
                      let height = this.videoHeight;
                      $('#curres' + remoteFeed.rfindex).removeClass('hide').text(width + 'x' + height).show();
                      if (Janus.webRTCAdapter.browserDetails.browser === 'firefox') {
                        // Firefox Stable has a bug: width and height are not immediately available after a playing
                        setTimeout(function() {
                          let width = $('#remotevideo' + remoteFeed.rfindex).get(0).videoWidth;
                          let height = $('#remotevideo' + remoteFeed.rfindex).get(0).videoHeight;
                          $('#curres' + remoteFeed.rfindex).removeClass('hide').text(width + 'x' + height).show();
                        }, 2000);
                      }
                    });
                  }
                  Janus.attachMediaStream($('#remotevideo' + remoteFeed.rfindex).get(0), stream);
                  let videoTracks = stream.getVideoTracks();
                  if (videoTracks === null || videoTracks === undefined || videoTracks.length === 0) {
                    // No remote video
                    $('#remotevideo' + remoteFeed.rfindex).hide();
                    if ($('#videoremote' + remoteFeed.rfindex + ' .no-video-container').length === 0) {
                      $('#videoremote' + remoteFeed.rfindex).append(
                        '<div class="no-video-container">' +
                        '<i class="fa fa-video-camera fa-5 no-video-icon"></i>' +
                        '<span class="no-video-text">No remote video available</span>' +
                        '</div>');
                    }
                  } else {
                    $('#videoremote' + remoteFeed.rfindex + ' .no-video-container').remove();
                    $('#remotevideo' + remoteFeed.rfindex).removeClass('hide').show();
                  }
                  if (!addButtons) {
                    return;
                  }
                  if (Janus.webRTCAdapter.browserDetails.browser === 'chrome' || Janus.webRTCAdapter.browserDetails.browser === 'firefox' ||
                    Janus.webRTCAdapter.browserDetails.browser === 'safari') {
                    $('#curbitrate' + remoteFeed.rfindex).removeClass('hide').show();
                    bitrateTimer[remoteFeed.rfindex] = setInterval(function() {
                      // Display updated bitrate, if supported
                      let bitrate = remoteFeed.getBitrate();
                      $('#curbitrate' + remoteFeed.rfindex).text(bitrate);
                      // Check if the resolution changed too
                      let width = $('#remotevideo' + remoteFeed.rfindex).get(0).videoWidth;
                      let height = $('#remotevideo' + remoteFeed.rfindex).get(0).videoHeight;
                      if (width > 0 && height > 0) {
                        $('#curres' + remoteFeed.rfindex).removeClass('hide').text(width + 'x' + height).show();
                      }
                    }, 1000);
                  }
                },*/
        onlocalstream: (stream) => {       // We have a local stream (getUserMedia worked!) to display
          if (this.video) {
            Janus.attachMediaStream(this.video, stream);
            this.logService.appendLog('INFO', 'on local stream :', stream);
          } else {
            this.logService.appendLog('ERROR', 'on local stream : DOM video element not found');
          }
        },
        onremotestream: (stream) => {       // We have a remote stream (working PeerConnection!) to display
          /*          if (this.remoteVideo) {*/
          if (id === 1) {
            Janus.attachMediaStream(this.remoteVideo, stream);
            this.streamRef.remoteVideoStream = stream;
          } else {
            Janus.attachMediaStream(this.remoteVideo2, stream);
            this.streamRef.remoteVideoStream2 = stream;
          }
          this.logService.appendLog('INFO', 'on remote stream :', stream);
          /*          } else {
                      this.logService.appendLog('ERROR', 'on remote stream : DOM video element not found');
                    }*/
        },
        oncleanup: () => {      // PeerConnection with the plugin closed, clean the UI. The plugin handle is still valid so we can create a new one
          this.logService.appendLog('INFO', 'on cleanup');
        },
        detached: () => {       // Connection with the plugin closed, get rid of its features. The plugin handle is not valid anymore
          this.logService.appendLog('INFO', 'detached');
        }
      });
  }

  /**/

  switchVideoContainers() {
    this.switchCam()();
  }

  switchCam() {
    return () => {
      if (this.streamRef.reverseOrder === true) {
        Janus.attachMediaStream(this.remoteVideo, this.streamRef.remoteVideoStream);
        Janus.attachMediaStream(this.remoteVideo2, this.streamRef.remoteVideoStream2);
      } else {
        Janus.attachMediaStream(this.remoteVideo, this.streamRef.remoteVideoStream2);
        Janus.attachMediaStream(this.remoteVideo2, this.streamRef.remoteVideoStream);
      }
      this.streamRef.reverseOrder = !this.streamRef.reverseOrder;
    };
  }

  // mostra i log in caso di errore
  showLogs() {
    if (this.logs) {
      this.logs = undefined;
    } else {
      this.logs = [];
      const subscription = this.logsBS.subscribe((next: any) => {
        this.logs.push(next);
      });
      subscription.unsubscribe();
    }
  }

  togglePipVisibility() {
    this.isPipVisible = !this.isPipVisible;
  }

  getCurrentName(current) {
    return current.firstName.charAt(0) + '.' + current.lastName;
  }

  ngOnDestroy(): void {
    this.janusService.detachJanusPlugin(JANUSPLUGIN.VIDEOROOM);
    this.statusDetailBSSubscription.unsubscribe();
  }
}
