import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';

@Component({
  selector: 'app-video-snapshot',
  templateUrl: './video-snapshot.component.html',
  styleUrls: ['./video-snapshot.component.scss']
})
export class VideoSnapshotComponent implements OnInit {
  @Input('video') video: ElementRef;
  @Input('title') title?: string;

  @Input() acquired: boolean;
  @Output() acquiredChange = new EventEmitter();

  @Input() formdata: FormData;
  @Output() formdataChange = new EventEmitter();

  @ViewChild('canvas_img') canvas: ElementRef;

  smallCanvas = false;

  private imageFormat = {
    dx: 0,
    dy: 0,
    dw: 1024, //320,
    dh: 768, //240,
    type: 'image/png'
  };


  constructor() {

  }

  ngOnInit() {
  }

  capture(canvas: ElementRef, video: ElementRef) {
    canvas.nativeElement.getContext('2d').drawImage(video, this.imageFormat.dx, this.imageFormat.dy, this.imageFormat.dw, this.imageFormat.dh);
    canvas.nativeElement.toDataURL(this.imageFormat.type);


    const blob = this.dataURItoBlob(canvas.nativeElement.toDataURL(this.imageFormat.type));
    this.formdata = new FormData();
    this.formdata.append('file', blob);
    this.formdataChange.emit(this.formdata);

    this.acquired = true;
    this.acquiredChange.emit(this.acquired);

  }

  dataURItoBlob(dataURI) {
    // convert base64/URLEncoded data component to raw binary data held in a string
    let byteString;
    if (dataURI.split(',')[0].indexOf('base64') < 0) {
      byteString = unescape(dataURI.split(',')[1]);
    } else {
      byteString = atob(dataURI.split(',')[1]);
    }

    // separate out the mime component
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

    // write the bytes of the string to a typed array
    const ia = new Uint8Array(byteString.length);
    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }

    return new Blob([ia], {type: mimeString});
  }

}
