import {Inject, Injectable} from '@angular/core';
import IJanusPluginHandle = JanusStatic.IJanusPluginHandle;
import IJanusOptions = JanusStatic.IJanusOptions;
import IJanusInitOptions = JanusStatic.IJanusInitOptions;
import IJanusPluginOptions = JanusStatic.IJanusPluginOptions;
import * as Janus from 'janus-typescript-client';
import {IJanusPluginsHandler} from '../interfaces/iJanusPluginHandler';


@Injectable({
  providedIn: 'root'
})
/**
 * Tengo il riferimento dei plugin creati,
 * perch al cambio delle pagine, non devo ricrearli ogni volta.
 * Se invece voglio ricrearli chiamo la detach in ngOnDestroy
 */
export class JanusService {

  private janus: JanusStatic;

  private janusPluginsHandler: IJanusPluginsHandler = {
    'janus.plugin.videoroom': {
      janus: undefined,
      janusPluginHandle: undefined
    },
    'janus.plugin.videocall': {
      janus: undefined,
      janusPluginHandle: undefined
    },
    'janus.plugin.echotest': {
      janus: undefined,
      janusPluginHandle: undefined
    }
  };

  constructor(@Inject('JANUS_SERVER_LIST') private JANUS_SERVER_LIST,
              @Inject('JANUS_SERVER_DEBUG') private JANUS_SERVER_DEBUG) {

  }

  getJanus(janusPluginName: string) {
    if (this.janusPluginsHandler && this.janusPluginsHandler[janusPluginName] && this.janusPluginsHandler[janusPluginName].janus) {
      return this.janusPluginsHandler[janusPluginName].janus;
    } else {
      return undefined;
    }
  }

  createJanusPluginHandle(janusPluginName: string, janusPluginOptions: IJanusPluginOptions, janusInitSuccessCallback?: () => void, janusInitErrorCallback?: (err: any) => void) {
    this.detachJanusPlugin(janusPluginName);
    const janusOption: IJanusOptions = {
      server: this.JANUS_SERVER_LIST,
      success: () => {
        this.janusPluginsHandler[janusPluginName].janus.attach(janusPluginOptions);
      },
      error: (err: Error) => {
        if (janusInitErrorCallback) {
          janusInitErrorCallback(err);
        }
      },
      destroyed: () => {
      }
    };
    const janusInitOptions: IJanusInitOptions = {
      // debug: false; /this.JANUS_SERVER_DEBUG,
      callback: () => {
        this.janusPluginsHandler[janusPluginName].janus = new Janus(janusOption);
        if (janusInitSuccessCallback) {
          janusInitSuccessCallback();
        }
      },
    };
    Janus.init(janusInitOptions);
  }

  setJanusPluginHandle(janusPluginName: string, janusPluginHandle: IJanusPluginHandle) {
    this.janusPluginsHandler[janusPluginName].janusPluginHandle = janusPluginHandle;
  }

  getJanusPluginHandle(janusPluginName: string) {
    return this.janusPluginsHandler[janusPluginName].janusPluginHandle;
  }

  detachJanusPlugin(janusPluginName: string) {
    try {
      this.janusPluginsHandler[janusPluginName].janusPluginHandle.detach();
      this.janusPluginsHandler[janusPluginName].janus.destroy();
      this.janusPluginsHandler[janusPluginName] = {
        janus: undefined,
        janusPluginHandle: undefined
      };
    } catch (e) {
    }
  }
}
