import {Injectable} from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {IVisitorCreateRequest} from '../../interfaces/iVisitorCreateRequest';
import {IVisitor} from '../../interfaces/iVisitor';
import {IVisitorAttachmentRequest} from '../../interfaces/iVisitorAttachmentRequest';
import {IVisitorAttachment} from '../../interfaces/iVisitorAttachment';

@Injectable({
  providedIn: 'root'
})
export class VisitorService extends HttpCommonService {

  private basePath = '/api/visitor/';

  get(idVisitor: string, skipErrorInterceptor?: boolean) {
    return this.http.get<IVisitor>(this.getServerUrl(this.basePath) + idVisitor, this.getOptions(skipErrorInterceptor));
  }

  create(data: IVisitorCreateRequest, skipErrorInterceptor?: boolean) {
    return this.http.post<IVisitor>(this.getServerUrl(this.basePath), data, this.getOptions(skipErrorInterceptor));
  }

  update(data: IVisitor, skipErrorInterceptor?: boolean) {
    return this.http.put<IVisitor>(this.getServerUrl(this.basePath), data, this.getOptions(skipErrorInterceptor));
  }

  delete(data: IVisitor, skipErrorInterceptor?: boolean) {
    const options = this.getOptions(skipErrorInterceptor);
    options['body'] = data;
    return this.http.delete<any>(this.getServerUrl(this.basePath), options);
  }

  sendToSolari(data: IVisitor, skipErrorInterceptor?: boolean) {
    return this.http.post<IVisitor>(this.getServerUrl(this.basePath) + 'send-to-solari', data, this.getOptions(skipErrorInterceptor));
  }

  attachmentCreate(data: IVisitorAttachmentRequest, skipErrorInterceptor?: boolean) {
    return this.http.post<IVisitorAttachment>(this.getServerUrl(this.basePath) + 'attachment', data, this.getOptions(skipErrorInterceptor));
  }

  attachmentUpdate(data: IVisitorAttachmentRequest, skipErrorInterceptor?: boolean) {
    return this.http.put<IVisitorAttachment>(this.getServerUrl(this.basePath) + 'attachment', data, this.getOptions(skipErrorInterceptor));
  }
}
