import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {SDesks} from '../../interfaces/sDesks';
import {SDesksCreate} from '../../interfaces/sDesksCreate';
import {BBranchesRequest} from '../../interfaces/bBrachesRequest';
import {HttpClient, HttpParams} from '@angular/common/http';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class SmartDesksService {
  bdata : SDesksCreate[];
  data : SDesks[];
  rootURL = environment.baseUrl

  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/smartdesk`);
  }

  get(id: number) {
    return this.http.get<SDesks>(`${this.rootURL}/find/smartdesks/branchid/` + id);
  }

  getCheckBookId(id: number) {
    return this.http.get<SDesks>(`${this.rootURL}/find/smartdesks/chequebook/` + id);
  }

  getCreditCardId(id: number) {
    return this.http.get<SDesks>(`${this.rootURL}/find/smartdesks/creditcard/` + id);
  }

  public create(bdata: SDesksCreate) {
    return this.http.post<SDesks>(`${this.rootURL}/save/smartdesk`, bdata);
  }


  update(data: SDesksCreate) {
    return this.http.post<SDesksCreate>(`${this.rootURL}/save/smartdesk`, data);
  }

  delete(id: SDesks) {
    return this.http.delete<SDesks>(`${this.rootURL}/delete/smartdesk/${id}`);
  }

  search(any: SDesks){
    this.http.get<SDesks>(`${this.rootURL}/search/smartdesk/${any}`);
  }

  // confirm(data: BBranches, skipErrorInterceptor?: boolean) {
  //   return this.http.post<BBranches>(this.getServerUrl(this.basePath) + 'confirm', data, this.getOptions(skipErrorInterceptor));
  // }
}
