import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Ng2SmartTableComponent } from 'ng2-smart-table/ng2-smart-table.component';
import { CCardList } from '../../interfaces/cCardList';
import { CreditCardListService } from '../../services/http/credit-card-list.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-credit-card-view',
  templateUrl: './credit-card-view.component.html',
  styleUrls: ['./credit-card-view.component.scss']
})
export class CreditCardViewComponent implements OnInit {

  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;

  errmsg:boolean = false;
  display_msg:any="";
  data:CCardList[];

  settings = {
      add:'',
      edit:{
        confirmSave:true
      },
      delete :'',
      actions: {
        position: 'right',
        columnTitle: 'Action',
      },

    columns: {
      id: {
        title: 'Id',
        filter: false,
        editable: false,
        addable: false
      },
      prod: {
        title: 'prod',
        filter: false,
        editable: true,
        addable: false
      },
      codicePan: {
        title: 'numCard',
        filter: false,
        editable: true,
        addable: false
      },
      stato: {
        title: 'stato',
        filter: false,
        editable: true,
        addable: false
      },
    }
  };

 id:any="";
  constructor(private http: HttpClient, private route: ActivatedRoute,  private cCardListService: CreditCardListService){
      this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      console.log(this.id);
      this.loadData();
    });
  }

 // Load data after every changes
  ngOnChanges(){
    //this.loadData();
  }

  // Load data when id is present
  loadData(){
    this.cCardListService.get(this.id).subscribe(res => {
          this.data = [res['data']];
          console.log(this.data);
          },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log("Client-side error occured.");
          } else {
            console.log("Server-side error occured.");
          }
      });
  }


  // update record in table
    updateRecord(event) {
      //console.log(event.newData);
      var udata = {
        "id" : event.newData.id,
        "prod" : event.newData.prod,
        "codicePan" : event.newData.codicePan,
        "stato" : event.newData.stato,
        "noOfSmartDesk" : null, "noOfIngestionTransaction": null, "noOfLodingTransaction":null, "noOfDeliveryTransaction":null
        };

      if(udata.stato !="" && udata.codicePan !="" && udata.stato != "" && isNaN(udata.stato)) {
            this.cCardListService.update(udata).subscribe(
            res => {
            // console.log(res);
              event.confirm.resolve(event.newData);
              this.loadData();
            },
            (err: HttpErrorResponse) => {
              if (err.error instanceof Error) {
                console.log("Client-side error occured.");
              } else {
              console.log("Server-side error occured.");
          }
        });
      }else{
        //alert("All fields are required !");
        this.errmsg = true;
        this.display_msg="All fields are required!";
        setTimeout (() => {
          this.errmsg = false;
        }, 2500);
      }
    }


    ngOnInit(){
      //this.loadData();
    };
}
