import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CBooksLoading} from '../../interfaces/cBooksLoading';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ChequeBookLoadingService {

  data : CBooksLoading[];
  rootURL = environment.baseUrl;
  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/chequebook/transaction/loading`);
  }

  getCheckBookId(id: number) {
    return this.http.get<CBooksLoading>(`${this.rootURL}/find/transaction/chequebook/loading/` + id);
  }
}
