import {Component, Input, OnInit} from '@angular/core';
import {ViewCell} from 'ng2-smart-table';
import {TranslateService} from '@ngx-translate/core';
import {IVisitor} from '../../../../interfaces/iVisitor';
import {VisitorService} from '../../../../services/http/visitor.service';
import {MediaService} from '../../../../services/http/media.service';

@Component({
  selector: 'app-visitors-getmedia-render',
  templateUrl: './visitors-getmedia-render.component.html',
  styleUrls: ['./visitors-getmedia-render.component.scss']
})
export class VisitorsGetmediaRenderComponent implements ViewCell, OnInit {
  visitor: IVisitor;

  @Input() value: any;
  @Input() rowData: any;

  constructor(private translate: TranslateService, private visitorService: VisitorService, private mediaService: MediaService) {
  }

  ngOnInit() {
    this.visitor = this.rowData;
  }


  onClick(event) {
    this.visitorService.get(this.visitor.id).subscribe(
      (response) => {
        this.visitor = response;
        this.visitor.attachments.forEach((attachment) => {
          this.mediaService.download(attachment.mediaId).subscribe((mediaResponse) => {
            this.downloadFile(mediaResponse, this.createFilename(this.visitor), 'png');
          });
        });
      }
    );
  }

  private createFilename(visitor: IVisitor) {
    return visitor.surname;
  }

  private downloadFile(arrayBufferResponse: any, filename: string, fileType: string) {
    const blob: Blob = new Blob([arrayBufferResponse]);
    /*        if (this.detectedBrowser.isIE) {          TODO
              window.navigator.msSaveOrOpenBlob(blob, 'DOMANDA.pdf');
            } else {*/
    const objectUrl = URL.createObjectURL(blob);
    const a: HTMLAnchorElement = document.createElement('a') as HTMLAnchorElement;

    a.href = objectUrl;
    a.download = filename + '.' + fileType;
    document.body.appendChild(a);
    a.click();

    document.body.removeChild(a);
    URL.revokeObjectURL(objectUrl);
    /*}*/
  }

}

