import { Injectable } from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {CBooksList} from '../../interfaces/cBooksList';
import {HttpClient, HttpParams} from '@angular/common/http';
import {CBooksIngestion} from '../../interfaces/cBooksIngestion';
import { environment } from '../../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ChequeBookIngestionService {

  data : CBooksIngestion[];
  rootURL = environment.baseUrl

  constructor(private http : HttpClient) { }

  getAll() {
       return this.http.get( `${this.rootURL}/find/chequebook/transaction/ingestion`);
  }

  getCheckBookId(id: number) {
    return this.http.get<CBooksIngestion>(`${this.rootURL}/find/transaction/chequebook/ingestion/` + id);
  }

}
