@Component({
  selector: 'app-cheque-books-view',
  templateUrl: './cheque-books-view.component.html',
  styleUrls: ['./cheque-books-view.component.scss']
})
export class ChequeBooksViewComponent implements OnInit {
  @Input('refresh') refresh?: boolean;
  @ViewChild('smartTable') smartTable: Ng2SmartTableComponent;

  errmsg:boolean = false;
  display_msg:any="";
  data:CBooksView[];

  settings = {
      add:'',
      edit:{
        confirmSave:true
      },
      delete :'',
      actions: {
        position: 'right',
        columnTitle: 'Action',
      },

    columns: {
        id: {
          title: 'Id',
          filter: false,
          editable: false,
          addable: false
        },
        cabAssegno: {
          title: 'cabAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        filAssegno: {
          title: 'filAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        nrPrimoAssegno: {
          title: 'nrPrimoAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        nrUltimoAssegno: {
          title: 'nrUltimoAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        nrAssegni: {
          title: 'nrAssegni',
          filter: false,
          editable: true,
          addable: false
        },
        cinPrimoAssegno: {
          title: 'cinPrimoAssegno',
          filter: false,
          editable: true,
          addable: false
        },
        codiceUnivoco: {
          title: 'codiceUnivoco',
          filter: false,
          editable: true,
          addable: false
        },
    }
  };

 id:any="";
  constructor(private http: HttpClient, private route: ActivatedRoute,  private CBookListService: ChequeBookListService){
      this.route.paramMap.subscribe(params => {
      this.id = params.get('id');
      console.log(this.id);
      this.loadData();
    });
  }

 // Load data after every changes
  ngOnChanges(){
    //this.loadData();
  }

  // Load data when id is present
  loadData(){
    this.CBookListService.get(this.id).subscribe(res => {
          this.data = [res['data']];
          console.log(this.data);
          },
        (err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            console.log("Client-side error occured.");
          } else {
            console.log("Server-side error occured.");
          }
      });
  }


  // update record in table
    updateRecord(event) {
      //console.log(event.newData);
      var udata = {
        "id" : event.newData.id,
        "cabAssegno" : event.newData.cabAssegno,
        "filAssegno" : event.newData.filAssegno,
        "nrPrimoAssegno" : event.newData.nrPrimoAssegno,
        "nrUltimoAssegno" : event.newData.nrUltimoAssegno,
        "nrAssegni" : event.newData.nrAssegni,
        "cinPrimoAssegno" : event.newData.cinPrimoAssegno,
        "codiceUnivoco":event.newData.codiceUnivoco,
        };

      if(udata.codiceUnivoco !="" && !isNaN(udata.codiceUnivoco) &&
          udata.cabAssegno !="" && !isNaN(udata.cabAssegno) &&
          udata.filAssegno != "" && !isNaN(udata.filAssegno) &&
          udata.nrPrimoAssegno !="" && !isNaN(udata.nrPrimoAssegno) &&
          udata.nrUltimoAssegno != "" && !isNaN(udata.nrUltimoAssegno) &&
          udata.nrAssegni !="" && !isNaN(udata.nrAssegni) &&
          udata.cinPrimoAssegno != "" && !isNaN(udata.cinPrimoAssegno)) {
            this.CBookListService.update(udata).subscribe(
            res => {
            // console.log(res);
              event.confirm.resolve(event.newData);
              this.loadData();
            },
            (err: HttpErrorResponse) => {
              if (err.error instanceof Error) {
                console.log("Client-side error occured.");
              } else {
              console.log("Server-side error occured.");
          }
        });
      }else{
        //alert("All fields are required !");
        this.errmsg = true;
        this.display_msg="All fields are required & must be Number !";
        setTimeout (() => {
          this.errmsg = false;
        }, 2500);
      }
    }

    onCustomAction(event){
      console.log(event);
    }

    ngOnInit(){
     // this.loadData();
    };


}

