import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IPagedData} from 'ngx-uaa/lib/interfaces/iPagedData';


@Component({
  selector: 'lib-dynamic-table-pagination',
  templateUrl: './dynamic-table-pagination.component.html',
  styleUrls: ['./dynamic-table-pagination.component.scss']
})
export class DynamicTablePaginationComponent implements OnInit {

  @Input() page: IPagedData;

  @Output() loadPageEventEmitter: EventEmitter<number> = new EventEmitter();

  enumArr = []; // [0,1,2,3,....,totalPages]

  linkArr = [];

  private readonly LINKS_NUMBER = 10;

  constructor() {
  }

  ngOnInit() {

  }

  loadData(pageNumber) {
    this.isInit(pageNumber);
    this.loadPageEventEmitter.emit(pageNumber);
  }

  loadNext() {
    this.shiftRight();
    this.loadPageEventEmitter.emit(this.page.number + 1);
  }

  loadPrevious() {
    this.shiftLeft();
    this.loadPageEventEmitter.emit(this.page.number - 1);
  }

  isInit(n) {
    if (this.enumArr.length <= 0 || this.enumArr.length !== this.page.totalPages) {
      this.enumArr = Array.from(Array(this.page.totalPages).keys());
    }
    if (n - (this.LINKS_NUMBER / 2) < 0) {
      this.linkArr = this.enumArr.slice(0, this.LINKS_NUMBER);
    } else {
      this.linkArr = this.enumArr.slice(n - (this.LINKS_NUMBER / 2), n + (this.LINKS_NUMBER / 2));
    }
    return true;
  }

  private shiftLeft() {
    if (this.linkArr[0] !== 0) {
      this.linkArr.unshift(this.linkArr[0] - 1);
      this.linkArr.pop();
    }
  }

  private shiftRight() {
    if (this.page.number < this.LINKS_NUMBER / 2) {
      return;
    }
    if (this.linkArr[this.linkArr.length - 1] < this.page.totalPages - 1) {
      this.linkArr.push(this.linkArr[this.linkArr.length - 1] + 1);
      this.linkArr.shift();
    }
  }
}
